import {
  useState, useEffect, useRef, useContext, useCallback,
} from 'react';
import { format } from 'timeago.js';

import { useDispatch, useSelector } from 'react-redux';
import 'emoji-mart/css/emoji-mart.css';
import { Picker } from 'emoji-mart';

import { IoCloseOutline, IoEarthSharp } from 'react-icons/io5';
import { VscClose } from 'react-icons/vsc';
import { AiOutlineSmile } from 'react-icons/ai';
import { authAxios } from '../../services/auth-header';
import {
  createPost,
  editPost,
  commentPost,
} from '../../Redux/actions/postActions';

import {
  CalenderSVG, ImageSVG, ProfileSVG, StarSVG,
} from '../../Assets/icons/SVG';

import { GlobalContext } from '../../context/GlobalContext';
import converFilesToFileBase from '../../Helper/convertFilesToUrl';

import './CreatePostModal.css';

function CreatePostModal() {
  const {
    isModalOpen, setIsModalOpen, currentId, setCurrentId,
  } = useContext(GlobalContext);

  const { user } = useSelector((state) => state.auth);
  const currentPost = useSelector((state) => (currentId
    ? state.post.posts.find((post) => currentId === post._id)
    : null));
  const {
    isEdit, setEdit, isComment, setIsComment,
  } = useContext(GlobalContext);

  const [ownerPost, setOwnerPost] = useState(null);
  const [mediaFilesLength, setMediaFilesLength] = useState('one');

  // ---------emojiis-----------
  const [showEmojis, setShowEmojis] = useState(false);

  const dispatch = useDispatch();

  const inputImageRef = useRef(null);
  const textAreaRef = useRef(null);

  const [post, setPost] = useState({
    createdBy: user?._id,
    caption: '',
    images: [],
    videosUrl: [],
    tags: [],
  });

  const onChange = (e) => {
    setPost({ ...post, [e.target.name]: e.target.value });
  };

  // handle upload file image (only)
  const handleSelectImageFile = () => {
    setShowEmojis(false);
    inputImageRef.current.click();
    if (inputImageRef) {
      const { files } = inputImageRef.current;

      // This is for storing the base64 strings

      // Convert files to BASE URL
      converFilesToFileBase(files, post, setPost);
    }
  };

  const addEmoji = (e) => {
    const sym = e.unified.split('-');
    const codesArray = [];
    sym.forEach((el) => codesArray.push(`0x${el}`));
    const emoji = String.fromCodePoint(...codesArray);
    // concat string with emojie
    setPost({ ...post, caption: post.caption + emoji });
  };

  const clear = () => {
    setPost({
      createdBy: user._id,
      caption: '',
      images: [],
      video: '',
      tags: [],
    });
    setCurrentId(null);
    setEdit(false);
    setIsComment(false);
    setShowEmojis(false);
    setMediaFilesLength('one');
  };

  const closeModel = () => {
    setIsModalOpen(false);
    clear();
  };

  useEffect(() => {
    if (currentPost) {
      if (isEdit) return setPost(currentPost);
    }
    return setPost([]);
  }, [currentPost, isEdit]);

  const handleSubmit = (e) => {
    e.preventDefault();
    // if form empty =>
    // if form valid
    if (post.caption?.trim() || post.images.length > 0) {
      // Edit post
      if (currentId && isEdit) {
        dispatch(editPost(currentId, post));
      }
      // post comment
      if (currentId && isComment) {
        const finalComment = {
          username: user.displaylName,
          body: post.caption,
          createdBy: user._id,
        };
        dispatch(commentPost(finalComment, currentPost._id));
      } else {
        // create post
        dispatch(createPost(post));
      }
      closeModel();
      clear();
    } else {
      console.log('empty');
    }
  };

  //  function submitOnEnter(event) {
  //     if (event.which === 13) {
  //        event.target.form.dispatchEvent(
  //           new Event('submit', { cancelable: true })
  //        );
  //        event.preventDefault();
  // Prevents the addition of a new line in the text field (not needed in a lot of cases)
  //     }
  //  }

  const removeImg = (index) => {
    // remove spesific item with index
    const currentFiles = post.images;
    currentFiles.splice(index, 1);
    setPost({
      ...post,
      images: currentFiles,
    });
  };

  // click outside form modal
  const handleClick = (e) => {
    const target = e.target.classList.contains('modal');
    if (target) return closeModel();
  };

  const toggleModal = () => {
    clear();
    setIsModalOpen(!isModalOpen);
  };

  // get user owner post
  useEffect(() => {
    let isSubscribed = true;
    const fetchUser = async () => {
      if (currentPost?.createdBy) {
        const { data } = await authAxios.get(
          `/users?userId=${currentPost.createdBy}`,
        );

        if (isSubscribed) {
          setOwnerPost(data.user);
        }
      }
    };
    fetchUser();
    return () => (isSubscribed = false);
  }, [currentPost]);

  const submitLogicUi = useCallback(
    () => {
      if (currentId && isComment) return 'Reply';
      if (currentId && isEdit) return 'Edit Post';

      return 'Create Post';
    },
    [currentId, isComment, isEdit],
  );

  useEffect(() => {
    submitLogicUi();
  }, [currentId, isComment, isEdit, submitLogicUi]);

  const imgLogicUi = useCallback(
    () => {
      if (post?.images?.length) {
        if (post.images.length > 4) {
          setMediaFilesLength('many');
        }
        switch (post?.images?.length) {
          case 1:
            return setMediaFilesLength('one');
          case 2:
            return setMediaFilesLength('two');
          case 3:
            return setMediaFilesLength('three');
          case 4:
            return setMediaFilesLength('four');
          default: return 'one';
        }
      }
    },
    [post?.images?.length],
  );

  useEffect(() => {
    imgLogicUi();
  }, [post.images?.length, imgLogicUi]);

  useEffect(() => {
    textAreaRef.current.focus();
  }, [isModalOpen]);

  return (
      <form
         onClick={handleClick}
         onSubmit={handleSubmit}
         className={`modal ${isModalOpen && 'active'}`}>
         <div className='create-post-modal'>
            <div className='header'>
               <IoCloseOutline onClick={toggleModal} />
            </div>
            {isComment && ownerPost && currentPost && (
               <div className='main comment'>
                  <div className='user-profile'>
                     {ownerPost.profilePicture ? (
                        <img
                           src={ownerPost.profilePicture}
                           alt='user profile image'
                        />
                     ) : (
                        <ProfileSVG />
                     )}
                  </div>
                  <div className='modal-container'>
                     <div className='modal-content'>
                        <div className='top-section'>
                           <h5 className='username'>
                              {ownerPost.displayName}
                           </h5>
                           <span className='text-muted'>
                              {` · ${format(currentPost.createdAt)}`}
                           </span>
                        </div>

                        <p className='post-caption'>
                           {currentPost.caption}
                        </p>
                        <span className='reply-to'>
                           Replying to @{ownerPost.username}
                        </span>
                     </div>
                  </div>
               </div>
            )}
            <div className='main'>
               <div className='user-profile'>
                  {user?.profilePicture ? (
                     <img
                        src={user.profilePicture}
                        alt='user profile image'
                     />
                  ) : (
                     <ProfileSVG />
                  )}
               </div>
               <div className='modal-container'>
                  <div className='modal-content'>
                     <textarea
                        ref={textAreaRef}
                        // onKeyPress={submitOnEnter}
                        onChange={(e) => onChange(e)}
                        value={post.caption}
                        name='caption'
                        className='text-area'
                        autoFocus
                        placeholder={
                           isComment
                             ? 'Reply'
                             : `What in your mind ${user.displayName}`
                        }
                     />
                     <input
                        hidden
                        ref={inputImageRef}
                        type='file'
                        accept='image/*,video/*'
                        multiple='multiple'
                        placeholder='Choose Image'
                        name='imageinput'
                        onChange={handleSelectImageFile}
                     />
                     <div
                        className={`mediafiles-preview ${mediaFilesLength}`}>
                        {post?.images?.map(({ imgUrl, createdAt }, index) => (
                           <div
                           data-index={index}
                           data-createdat={createdAt}
                              key={index}
                              // data-count-files={`+${
                              //   post?.images?.length - 4 || '+1'
                              // }`}
                              className='media-preview'>
                              {imgUrl && (
                                 <img
                                    onClick={handleSelectImageFile}
                                    src={imgUrl}
                                    alt='media post'
                                 />
                              )}
                              <IoCloseOutline
                                 onClick={() => removeImg(index)}
                                 className='close'
                              />
                           </div>
                        ))}
                     </div>
                     {!isComment && (
                        <div className='post-status'>
                           <IoEarthSharp />
                           <span>Everyone can reply</span>
                        </div>
                     )}
                  </div>
                  <div className='footer'>
                     <div className='icons'>
                        <div
                           className='choose-image'
                           onClick={handleSelectImageFile}>
                           <ImageSVG />
                        </div>
                        <div className='choose-calender'>
                           <CalenderSVG />
                        </div>
                        <div className='choose-emojie'>
                           {showEmojis && (
                              <Picker
                                 onSelect={addEmoji}
                                 style={{
                                   width: '266px',
                                   position: 'absolute',
                                   top: '261px',
                                   left: '176px',
                                   height: '311px',
                                   overflow: 'hidden',
                                 }}

                              />
                           )}
                           <div
                              onClick={() => setShowEmojis(!showEmojis)
                              }>
                              <AiOutlineSmile />
                           </div>
                        </div>
                     </div>

                     <button
                        type='submit'
                        className='btn btn-primary'
                        // disabled={
                        //    !post?.caption?.trim() && !post?.images[0]
                        // }
                        >
                        {submitLogicUi()}
                     </button>
                  </div>
               </div>
            </div>
         </div>
      </form>
  );
}

export default CreatePostModal;
