import { authAxios } from '../../services/auth-header';

import {
  GET_TIMELINE_POSTS,
  ADD_POST,
  LIKE_POST,
  DELETE_POST,
  EDIT_POST,
  SAVE_POST,
  SET_COMMENT,
  REMOVE_COMMENT,
  SET_LOADING_POSTS,
  SET_POST_LOADING,
} from '../types/action-types';

export const getUserProfilePosts = (username) => async (dispatch) => {
  dispatch({ type: SET_LOADING_POSTS });
  try {
    const { data } = await authAxios.get(
      `/posts/profile/${username}`,
    );
    dispatch({ type: GET_TIMELINE_POSTS, payload: data.posts });
  } catch (error) {
    console.error(error);
  }
};

export const getTimelinePosts = (userId) => async (dispatch) => {
  dispatch({ type: SET_LOADING_POSTS });
  try {
    const { data } = await authAxios.get(
      `/posts/timeline/${userId}`,
    );
    dispatch({ type: GET_TIMELINE_POSTS, payload: data.posts });
  } catch (error) {
    console.error(error);
  }
};

export const createPost = (post) => async (dispatch) => {
  // dispatch({ type: SET_POST_LOADING });
  try {
    const { data } = await authAxios.post('/posts/', post);
    dispatch({ type: ADD_POST, payload: data.post });
  } catch (error) {
    console.error(error);
  }
};

export const editPost = (postId, newPost) => async (dispatch) => {
  try {
    const { data } = await authAxios.patch(
      `/posts/${postId}`,
      newPost,
    );
    dispatch({ type: EDIT_POST, payload: data.post });
  } catch (error) {
    console.error(error);
  }
};

export const likePost = (postId, userId) => async (dispatch) => {
  try {
    await authAxios.put(`/posts/${postId}/like`, { userId });
    dispatch({ type: LIKE_POST, payload: { postId, userId } });
  } catch (error) {
    console.error(error);
  }
};

export const deletePost = (postId) => async (dispatch) => {
  try {
    await authAxios.delete(`/posts/${postId}`);
    dispatch({ type: DELETE_POST, payload: postId });
  } catch (error) {
    console.error(error);
  }
};

export const savePost = (postId, userId) => async (dispatch) => {
  try {
    // await authAxios.put(`/posts/${postId}/save`, { userId });
    dispatch({ type: SAVE_POST });
  } catch (error) {
    console.error(error);
  }
};

export const commentPost = (value, postId) => async (dispatch) => {
  try {
    const { data } = await authAxios.post(
      `/posts/${postId}/commentPost`,
      value,
    );
    dispatch({ type: SET_COMMENT, payload: data.updatedPost });
    return data.updatedPost.comments;
  } catch (error) {
    console.error(error);
  }
};

export const removeComment = (commentId, postId) => async (dispatch) => {
  dispatch({
    type: REMOVE_COMMENT,
    payload: { postId, commentId },
  });
  try {
    const { data } = await authAxios.patch(
      `/posts/${postId}/comments/${commentId}/deleteComment`,
    );
  } catch (error) {
    console.error(error);
  }
};
