import { useEffect } from 'react';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from 'react-router-dom';
import { Provider } from 'react-redux';
import store from './Redux/store/store';

import Navbar from './Parts/Header/Navbar';
import Home from './Pages/Home';
import Profile from './Pages/profile';
import Register from './Auth/Register';
import PostDetails from './Pages/PostDetails';
import Messenger from './Pages/Messenger';
import Call from './components/Call/index';

import { getUser } from './Redux/actions/userActions';
import { getTimelinePosts } from './Redux/actions/postActions';
import { getConversations } from './Redux/actions/conversationActions';

import GlobalComponent from './components/GlobalComponent';
import ErrorPage from './Pages/Error/ErrorPage.jsx';

function App() {
  const token = JSON.parse(localStorage.getItem('token'));
  const userId = JSON.parse(localStorage.getItem('userId'));

  // if account exists get user data =>
  useEffect(() => {
    if (userId) {
      store.dispatch(getUser(userId));
      store.dispatch(getTimelinePosts(userId));
      store.dispatch(getConversations(userId));
    }
  }, [userId]);

  return (
      <Router>
         <Provider store={store}>
            <GlobalComponent />
          <Navbar />
            <Routes>
               <Route path={'/*'} element={<Home />} />
               <Route
                  path='/profile/:id'
                  element={token ? <Profile /> : <Navigate to='/' />}
                  />
                  <Route path='/post/:id' element={<PostDetails />} />
               <Route path='/messenger' element={<Messenger />} />
            <Route path='/chat' element={<Call />} />
               <Route
                  path='/auth'
                  element={
                     !token ? <Register /> : <Navigate to='/' />
                  }
               />
               <Route path='*' element={<ErrorPage />} />
            </Routes>
         </Provider>
      </Router>
  );
}

export default App;
