import { useEffect, useContext } from 'react';
import { useSelector } from 'react-redux';
import Stories from '../../Parts/Stories/Stories';
import Feed from '../../components/Feed/index';
import CreatePost from '../../components/CreatePost';
import Messages from '../../components/Conversations';
import Left from '../../Parts/left/Left';
import { GlobalContext } from '../../context/GlobalContext';

import Static from '../../Static-website/Static';
import OnlineFriends from '../../components/OnlineFriends/index';
import UserFriends from '../../components/UserFriends/index';

function Home() {
  const { setIsModalOpen, currentId, setCurrentId } = useContext(GlobalContext);
  const { token } = useSelector((state) => state.auth);

  useEffect(() => {
    if (currentId) return setIsModalOpen(true);
  }, [currentId, setIsModalOpen]);

  if (!token) {
    return <Static />;
  }

  return (
    <main>
      <div className='container'>
        <Left setIsModalOpen={setIsModalOpen} />
        <div className='middle'>
          <Stories />
          <CreatePost currentId={currentId} />
          <Feed currentId={currentId} setCurrentId={setCurrentId} />
        </div>
        <div className='right'>
          <Messages />
          <UserFriends title='Friends' />
          <OnlineFriends />
        </div>
      </div>
    </main>
  );
}

export default Home;
