import {
  GET_TIMELINE_POSTS,
  ADD_POST,
  LIKE_POST,
  DELETE_POST,
  EDIT_POST,
  SAVE_POST,
  SET_COMMENT,
  REMOVE_COMMENT,
  SET_POST_LOADING,
  SET_LOADING_POSTS,
} from '../types/action-types';

const initialState = {
  posts: [],
  isLoading: false,
  isPostLoading: false,
};

const postReducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_TIMELINE_POSTS:
      return {
        ...state,
        isLoading: false,
        posts: action.payload.sort((p1, p2) => new Date(p2.createdAt) - new Date(p1.createdAt)),
      };
    case ADD_POST:
      return { ...state, isPostLoading: false, posts: [action.payload, ...state.posts] };
    case LIKE_POST:
    case EDIT_POST:
      return {
        ...state,
        posts: state.posts.map((post) => (post._id === action.payload._id ? action.payload : post)),
      };
    case SET_COMMENT:
      return {
        ...state,
        posts: state.posts.map((post) => {
          if (post._id === action.payload._id) {
            return action.payload;
          }
          return post;
        }),
      };
    case REMOVE_COMMENT:
      return {
        ...state,
        posts: state.posts.map((post) => ({
          ...post,
          comments: post.comments.filter(
            (comment) => comment._id !== action.payload.commentId,
          ),
        })),
      };
    case SAVE_POST:
      return {
        ...state,
      };
    case DELETE_POST:
      return {
        ...state,
        posts: state.posts.filter((post) => post._id !== action.payload),
      };
    case SET_POST_LOADING:
      return { ...state, isPostLoading: true };

    case SET_LOADING_POSTS:
      return { ...state, isLoading: true };
    default:
      return state;
  }
};

export default postReducer;
