export const GlobalContext = createContext(INITIAL_STATE);

const socket = io();
// const socket = io('localhost:8080');
// const socket = io();
export function AuthContextProvider({ children }) {
  const [state, dispatch] = useReducer(AuthReducer, INITIAL_STATE);

  // post is edit or is Comment
  const [isEdit, setEdit] = useState(false);
  const [isComment, setIsComment] = useState(false);

  // frined user
  const [currentId, setCurrentId] = useState(null);
  // frined user in in conversation (i talk to him)
  const [userFriend, setuserFriend] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isChatBoxPopupOpen, setIsChatBoxPopupOpen] = useState(true);

  // auth alert
  const [authAlert, setAuthAlert] = useState(false);
  const [isRoomModalOpen, setIsRoomModalOpen] = useState(false);

  // ------------ video Chat
  const [callAccepted, setCallAccepted] = useState(false);
  const [callEnded, setCallEnded] = useState(false);
  const [stream, setStream] = useState();
  const [me, setMe] = useState('');

  const [call, setCall] = useState({});

  const myVideo = useRef();
  const myAudio = useRef();
  const userVideo = useRef();
  const connectionRef = useRef();

  const startCall = (userId) => {
    navigator.mediaDevices
      .getUserMedia({ video: false, audio: true })
      .then((currentStream) => {
        setStream(currentStream);
        // myVideo.current.srcObject = currentStream;
        myAudio.current.srcObject = currentStream;
      });

    socket.on('me', (id) => setMe(id));

    socket.on('callUser', ({ from, signal }) => {
      setCall({
        isReceivingCall: true,
        from,
        name: 'yesmine attia',
        signal,
      });
    });
  };

  const callUser = (id, from) => {
    const peer = new Peer({
      initiator: true,
      trickle: false,
      stream,
    });

    peer.on('signal', (data) => {
      socket.emit('callUser', {
        userToCall: id,
        signalData: data,
        from,
        name: 'yesmine',
      });
    });

    peer.on('stream', (currentStream) => {
      userVideo.current.srcObject = currentStream;
    });

    socket.on('callAccepted', (signal) => {
      setCallAccepted(true);

      peer.signal(signal);
    });

    connectionRef.current = peer;
  };

  const answerCall = () => {
    setCallAccepted(true);

    const peer = new Peer({
      initiator: false,
      trickle: false,
      stream,
    });

    peer.on('signal', (data) => {
      socket.emit('answerCall', { signal: data, to: call.from });
    });

    peer.on('stream', (currentStream) => {
      userVideo.current.srcObject = currentStream;
    });

    peer.signal(call.signal);

    connectionRef.current = peer;
  };

  const leaveCall = () => {
    setCallEnded(true);

    connectionRef.current.destroy();

    window.location.reload();
  };

  return (
      <GlobalContext.Provider
         value={{
           socket,
           user: state.user,
           isFetching: state.isFetching,
           error: state.error,
           dispatch,
           isEdit,
           setEdit,
           isComment,
           setIsComment,
           userFriend,
           setuserFriend,
           isModalOpen,
           setIsModalOpen,
           currentId,
           setCurrentId,
           authAlert,
           setAuthAlert,
           isChatBoxPopupOpen,
           setIsChatBoxPopupOpen,
           setIsRoomModalOpen,
           isRoomModalOpen,
           call,
           callAccepted,
           me,
           myAudio,
           userVideo,
           stream,
           callEnded,
           callUser,
           leaveCall,
           answerCall,
           startCall,
         }}>
         {children}
      </GlobalContext.Provider>
  );
}
