import { useSelector, useDispatch } from 'react-redux';
import React, { useEffect } from 'react';

import { EditSquareSVG, SearchSVG } from '../../Assets/icons/SVG';

import Conversation from './Conversation';
import { MessagesSkeleton } from '../skeleton/MessagesSkeleton';
import SearchBar from '../../Parts/Header/Navbar/SearchBar';
import StaticMessages from '../../Static-website/StaticMessages';
import OnlineFriends from '../OnlineFriends';

function Conversations() {
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.auth);

  const { conversations, isLoading } = useSelector(
    (state) => state.conversation,
  );

  if (conversations?.length === 0) {
    return <StaticMessages/>;
  }

  return (
    <>
      <div className={`messages ${isLoading && 'skeleton'}`}>
        <div className="heading">
          <h4>Messages</h4>
          <EditSquareSVG />
        </div>
        <SearchBar/>
        {/* ---------- MESSAGES CATEGORY ------------*/}
        <div className="category">
          <h6 className="active">Primary</h6>
          <h6>General</h6>
          <h6 className="message-requests">Requests(7)</h6>
        </div>

        {/* --- MESSAGE ---*/}
        {
          conversations
            .slice(0, 5)
            .map((c) => (
              <Conversation
                key={c._id}
                conversation={c}
                currentUser={user}
              />
            ))
       }
      </div>
      {/* ---------- END OF MESSAGES ------------*/}
    </>
  );
}

export default React.memo(Conversations);
