import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { ProfileSVG } from '../../../Assets/icons/SVG';
import './user-card.css';
import {
  followUser,
  unfollowUser,
} from '../../../Redux/actions/authActions';

function UserCard({
  _id,
  profilePicture,
  username,
  displayName,
  about,
  followings,
  followers,
  currentUserId,
  popupOpen,
}) {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleBtnLocgicUi = () => {
    if (currentUserId === _id) {
      return (
        <button
          onClick={() => {
            dispatch(unfollowUser(currentUserId, _id));
          }}
          className="btn btn-primary"
        >
          Me
        </button>
      );
    }
    if (followers?.includes(currentUserId)) {
      return (
        <button
          onClick={() => {
            dispatch(unfollowUser(currentUserId, _id));
          }}
          className="btn btn-primary"
        >
          Unfollow
        </button>
      );
    }
    return (
      <button
        onClick={() => {
          dispatch(followUser(currentUserId, _id));
        }}
        className="btn btn-primary"
      >
        Follow
      </button>
    );
  };

  return (
    <div className={`user-card-popup ${popupOpen && 'active'}`}>
      <div
        className="user-card-popup-container bg-color"
        onClick={() => {
          navigate(`/profile/${username}`);
        }}
      >
        <div className="header">
          <div className="profile-picture">
            {profilePicture ? (
              <img src={profilePicture} />
            ) : (
              <ProfileSVG />
            )}
          </div>
          <div
            className="btn-container"
            onClick={(e) => e.stopPropagation()}
          >
            {handleBtnLocgicUi()}
          </div>
        </div>
        <div className="user-info">
          <h3 className="username">{displayName}</h3>
          <span className="text-muted">
            @
            {username}
          </span>
          <p className="caption">{about}</p>
        </div>
        <div className="follow-section">
          <li>
            <span className="follow-count">
              <b>{followings?.length}</b>
              {' '}
              Following
            </span>
          </li>
          <li>
            <span className="follow-count">
              <b>{followers?.length}</b>
              {' '}
              Followers
            </span>
          </li>
        </div>
      </div>
    </div>
  );
}

export default UserCard;
