import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  IoArrowBackOutline,
  IoArrowForward,
  IoCloseOutline,
  IoDesktopOutline,
} from 'react-icons/io5';
import CommentSection from '../../components/post/Comments/CommentsSection';
import ActionButtons from '../../components/post/ActionButtons/index';
import User from '../../components/post/PostOwner/User';

function PostDetails({
  itemIndex,
  mediaFiles,
  user,
  post,
  setOpenPostDetails,
  handleLikeBtn,
  isLiked,
}) {
  const {
    _id, caption, likes, comments,
  } = post;
  const [index, setIndex] = useState(itemIndex || 0);
  const activeItem = mediaFiles[index];
  const navigate = useNavigate();

  useEffect(() => {
    setIndex(itemIndex);
  }, [itemIndex]);

  const checkNumber = (number) => {
    if (number > mediaFiles.length - 1) {
      return 0;
    }
    if (number < 0) return mediaFiles.length - 1;
    return number;
  };

  const nextItem = () => {
    setIndex((prev) => checkNumber(prev + 1));
  };

  const prevItem = () => {
    setIndex((prev) => checkNumber(prev - 1));
  };

  return (
      <section className='post-details'>
         <div className='post-details-container'>
            <div className='left-section'>
               <div className='image-container'>

                  {activeItem?.imgUrl ? (
                     <img
                     src={activeItem.imgUrl}
                     alt='post image with details'
                  />
                  ) : (
                     <video
                     src={activeItem.videoUrl}
                     alt='post image with details'
                  />
                  )}
               </div>
               <div className='btns'>
                  <button
                     onClick={() => {
                     //   navigate('/');
                       setOpenPostDetails(false);
                     }}
                     className='btn-close'>
                     <IoCloseOutline />
                  </button>
                  <button className='btn btn-full-secreen' />
                  <IoDesktopOutline />
                  <button
                     onClick={prevItem}
                     className='prev btn-prev'>
                     <IoArrowForward />
                  </button>
                  <button
                     onClick={nextItem}
                     className='next btn-next'>
                     <IoArrowBackOutline />
                  </button>
               </div>
            </div>
            <aside className='right-section'>
               <div className='right-section-container'>
                  <section className='top-section'>
                     <User post={post} user={user} />
                     <p className='caption'>{caption}</p>
                     <div className='more-info text-muted'>
                        <span className='date '>
                           7:00 PM · Dec 5, 2021
                        </span>
                        <span className='date'>Postyy Web App</span>
                     </div>
                     <ul className='details-section'>
                        <li>
                           <span className='details-count'>
                              <b> {likes.length} </b> Likes
                           </span>
                        </li>
                        <li>
                           <span className='details-count'>
                              <b>22 </b> Shares
                           </span>
                        </li>
                        <li>
                           <span className='details-count'>
                              <b>213 </b> Saves
                           </span>
                        </li>
                     </ul>
                     <ActionButtons
                        postId={_id}
                        handleLikeBtn={handleLikeBtn}
                        isLiked={isLiked}
                     />
                  </section>
                  <section className='bottom-section'>
                     <CommentSection
                        postId={_id}
                        comments={comments}
                     />
                  </section>
               </div>
            </aside>
         </div>
      </section>
  );

  // return (
  //   <div
  //     style={{
  //       position: 'absolute',
  //       height: '100px',
  //       backgroundColor: 'green',
  //     }}
  //   >
  //     <img width="40px" height="40px" src={activeItem} alt="" />
  //     <button onClick={nextItem}>next</button>
  //     <button onClick={prevItem}>prev</button>
  //     <img
  //       src={user.profilePicture}
  //       width="40px"
  //       height="40px"
  //       alt=""
  //     />
  //     <p>{caption}</p>
  //     <p>{user.displayName}</p>
  //     <CommentSection postId={postId} postComments={comments} />
  //   </div>
  // );
}

export default React.memo(PostDetails);
