import { useEffect, memo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Post from '../post/index';
import {
  getTimelinePosts,
  getUserProfilePosts,
} from '../../Redux/actions/postActions';
import PostSkeleton from '../skeleton/PostSkeleton';

function Feed({ username }) {
  const { user } = useSelector((state) => state.auth);
  const { posts, isLoading, isPostLoading } = useSelector((state) => state.post);
  const dispatch = useDispatch();

  //   console.log('Feed Call');

  // get posts
  useEffect(() => {
    const fetchPosts = () => {
      username
        ? dispatch(getUserProfilePosts(username))
        : user?._id && dispatch(getTimelinePosts(user._id));
    };
    fetchPosts();
  }, [username, user._id, dispatch]);

  // loading Fedd Skeleton
  if (isLoading) {
    return <>
     <PostSkeleton />
     <PostSkeleton />
     </>;
  }

  //  Time Line Empty
  if (posts.length === 0) {
    return (
         <div className='feeds'>
            <section className='no-posts'>
               <h3 className='title'>Welcome to Posty!</h3>
               <p className='caption'>
                  <b>
                     <a
                        href='https://github.com/aminbenz/'
                        target='_blank'
                        rel='noreferrer'>
                        Postyy
                     </a>
                  </b>{' '}
                 A Social Media Website Made For Education Purpose By
                  <b>
                     {' '}
                     <a
                        href='https://www.facebook.com/aminbenzz/'
                        target='_blank'
                        rel='noreferrer'>
                        Amin Benz{' '}
                     </a>
                  </b>
                  Your TimeLine Is Empty Let&apos;s Get Started ,Try To Search Friends :)
               </p>
               <button className='btn btn-primary'>Show me</button>
            </section>
         </div>
    );
  }

  return (
      <div className='feeds'>
         { isPostLoading && <PostSkeleton loader={true}/>}
         {posts.map((post) => (
            <Post post={post} key={post._id} />
         ))}
      </div>
  );
}

export default memo(Feed);
