import {
  useState,
  useEffect,
  useRef,
  useContext,
  useCallback,
} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  useLocation,
  useNavigate,
  Link,
} from 'react-router-dom';

import queryString from 'query-string';

import './chat-box.css';

import { IoClose, IoPeopleOutline } from 'react-icons/io5';
import {
  getConversationMessages,
  setCurrentConversation,
} from '../../Redux/actions/conversationActions';
import ChatForm from './ChatForm.jsx';
import { PopupMessagesSkeleton } from '../skeleton/MessagesSkeleton';
import {
  ArrowDawn,
  CallSVG,
  VideoCall,
  ProfileSVG,
  CamSVG,
  ImageSVG,
  SendSVG,
} from '../../Assets/icons/SVG.js';
import Message from './Message/index';
import ChatRoom from './ChatRoom';
// import Call from '../Call/index.js';
import { authAxios } from '../../services/auth-header';
import { GlobalContext } from '../../context/GlobalContext';

function ChatBox() {
  const {
    socket, isChatBoxPopupOpen, setIsChatBoxPopupOpen, callUser, startCall, me,
  } = useContext(GlobalContext);
  const { user } = useSelector((state) => state.auth);
  const { currentConversation, isLoading } = useSelector(
    (state) => state.conversation,
  );
  const { onlineFriends } = useSelector((state) => state.user);
  const dispatch = useDispatch();

  const [friendConv, setFriendConv] = useState(null);
  const [messages, setMessages] = useState([]);
  const [arrivallMessage, setArrivalMessage] = useState(null);
  const [unreadMsgs, setUnreadMsges] = useState(0);
  const [userTyping, setUserTyping] = useState(false);

  const audioMessageRef = useRef();
  const location = useLocation();
  const navigate = useNavigate();
  const viewRef = useRef(null);

  const [isRoom, setIsRoom] = useState(false);
  const [message, setMessage] = useState([]);
  const [isRoomDataOpen, setIsRoomDataOpen] = useState(false);
  const [name, setName] = useState('');
  const [room, setRoom] = useState('');
  const [usersInRoom, setUsersInRoom] = useState([]);

  const [isCalling, setIsCalling] = useState(false);

  // GET_CHAT_MESSAGES
  useEffect(() => {
    let isFetching = true;
    const initializationConversation = async () => {
      const chatMessages = await dispatch(
        getConversationMessages(currentConversation._id),
      );
      if (isFetching) {
        setMessages(chatMessages);
      }
      const friendId = currentConversation?.members.find(
        (member) => member !== user._id,
      );

      if (friendId) {
        // const userFriend = await dispatch(getUser(friendId));
        // setFriendConv(userFriend);
        const { data } = await authAxios.get(
          `/users?userId=${friendId}`,
        );
        if (isFetching) {
          setFriendConv(data.user);
        }
      }
    };
    currentConversation?._id && initializationConversation();
    return () => {
      isFetching = false;
    };
  }, [currentConversation, dispatch, user._id]);

  // currentConversation;

  // message sound effect
  const playSoundEffect = (src) => {
    if (src && audioMessageRef.current) {
      audioMessageRef.current.play().catch((error) => {
        console.error(error);
        //  when an exception is played, the exception flow is followed
      });
    }
  };

  // Soket - PRIVATE - ReciveMessage
  useEffect(() => {
    socket.on('getMessage', ({ senderId, text, conversation }) => {
      setArrivalMessage({
        sender: senderId,
        text,
        createdAt: Date.now(),
      });

      dispatch(setCurrentConversation(conversation));

      // play sound when recive resive message
      playSoundEffect('/audio/get-message.wav');

      setUnreadMsges((prev) => prev + 1);
    });
    socket.on('isTyping', ({ typing }) => {
      setUserTyping(typing);
    });

    // return () => {
    //    socket.emit("disconnect");
    //    socket.off();
    // };
  }, [socket, dispatch]);

  useEffect(() => {
    arrivallMessage
         && currentConversation?.members.includes(
           arrivallMessage.sender,
         )
         && setMessages((prev) => [...prev, arrivallMessage]);
  }, [arrivallMessage, currentConversation]);

  // scroll to viwe when send message
  useEffect(() => {
    viewRef?.current?.scrollTo(0, viewRef?.current?.scrollHeight, {
      behavior: 'smooth',
    });
  }, [messages, currentConversation, userTyping]);

  useEffect(() => {
    setUnreadMsges(0);
  }, [isChatBoxPopupOpen]);

  const startRoom = useCallback(() => {
    if ((name, room)) {
      setMessages([]);
      setIsRoom(true);
      socket.emit('join room', { name, room }, () => {});
      socket.on('roomData', ({ room, onlineUsers }) => {
        setRoom(room);
        setUsersInRoom(onlineUsers);
      });
    }
    // return () => {
    //    socket.emit('disconnect');
    //    socket.off();
    // };
  }, [room, socket, name]);

  useEffect(() => {
    const { name, room } = queryString.parse(location.search);
    if (name && room) {
      setName(name);
      setRoom(room);
      startRoom();
    }
  }, [socket, location.search, startRoom]);

  useEffect(() => {
    socket.on('message room', (message) => {
      setMessages([...messages, message]);
    });
  }, [messages, socket]);

  const sendMessage = (e) => {
    e.preventDefault();
    if (message) {
      socket.emit('sendMessage room', message, () => {});
      setMessage('');
    }
  };

  const exitRoom = () => {
    setMessages([]);
    setIsRoom(false);
    navigate('/');
  };

  // laod skeleton messages
  if (isLoading && currentConversation) {
    return <PopupMessagesSkeleton isChatBoxPopupOpen={isChatBoxPopupOpen} />;
  }

  if (isRoom === false && !currentConversation) {
    return <div></div>;
  }

  const handleCall = (friendId) => {
    navigate('/chat');
    setIsCalling(true);
    startCall(user.id);
    callUser(friendId, user.id);
  };

  return (
      <section
         data-unread-messages={unreadMsgs}
         className={`conversation-box-popup ${
           isChatBoxPopupOpen === false && 'mini'
         } ${unreadMsgs > 0 && 'msgs'}`}>
           {/* <Call/> */}
         <audio
            ref={audioMessageRef}
            hidden
            src='/audio/send-message.mp3'
         />
         <div
            className='user-mini'
            data-name={
               (room && room[0]) || friendConv?.displayName[0]
            }
            onClick={() => setIsChatBoxPopupOpen(!isChatBoxPopupOpen)}>
            {friendConv?.profilePicture ? (
               <img
                  src={friendConv.profilePicture}
                  alt={`${user.displayName}Profile Image`}
               />
            ) : (
               <ProfileSVG />
            )}
         </div>
         <div className='header'>
            {isRoom ? (
               <>
                  {usersInRoom?.length - 1 > 0 ? (
                     <ul
                        className={`room-data ${
                          isRoomDataOpen && 'active'
                        }`}>
                        <h3 className='room-title'>Users In Room</h3>
                        { usersInRoom.map(({ id, name }) => {
                          const url = queryString.parse(
                            location.search,
                          );
                          return (
                                 <li key={id}>
                                    {url.name === name ? 'You' : name}
                                 </li>
                          );
                        })}
                     </ul>
                  ) : (
                     <ul
                        //  style={{ top: '-30px' }}
                        className={`room-data ${
                          isRoomDataOpen && 'active'
                        }`}>
                        <h4 className=''>No Users In this Room</h4>
                     </ul>
                  )}
                  <div
                     className='user'
                     onMouseOver={
                        isRoom ? () => setIsRoomDataOpen(true) : null
                     }
                     onMouseLeave={
                        isRoom ? () => setIsRoomDataOpen(false) : null
                     }>
                     <div className='image-container'>
                        <IoPeopleOutline />
                     </div>
                     <div className='user-info'>
                        <h3 className='username'>{room}</h3>
                        <p className='status'>
                           {usersInRoom?.length - 1 || '0'} Users in Room
                        </p>
                     </div>
                  </div>
               </>
            ) : (
               <Link
                  to={`profile/${friendConv?.username}`}
                  className='user'>
                  <div className='image-container'>
                     {isRoom ? (
                        <IoPeopleOutline />
                     ) : friendConv?.profilePicture ? (
                        <img
                           src={friendConv.profilePicture}
                           alt={`${friendConv.displayName}Profile Image`}
                        />
                     ) : (
                        <ProfileSVG />
                     )}
                  </div>
                  {isRoom ? (
                     <div className='user-info'>
                        <h3 className='username'>{room}</h3>
                        <p className='status'>
                           {usersInRoom?.length - 1 === 0 } Users in Room
                        </p>
                     </div>
                  ) : (
                     <div className='user-info'>
                        <h3 className='username'>
                           {friendConv?.displayName}
                        </h3>
                        <p className='status'>
                           {`${onlineFriends.map((friend) => (friend?._id === user?._id
                             ? 'Online now'
                             : 'Ofline Now'))}`}
                        </p>
                     </div>
                  )}
               </Link>
            )}

            <div className='right-section'>
               <div className='icons'>
                  <div onClick={() => callUser(friendConv?._id, user._id)}>
                  <VideoCall />
                  </div>
                  <div onClick={() => handleCall(friendConv?._id, user._id)}>
                     <CallSVG />
                  </div>
                  <div onClick={() => setIsChatBoxPopupOpen(!isChatBoxPopupOpen)}>
                     <ArrowDawn />
                  </div>

                  {isRoom ? (
                     <div href='/' onClick={exitRoom}>
                        <IoClose />
                     </div>
                  ) : (
                     <div onClick={() => setCurrentConversation([])}>
                        <IoClose />
                     </div>
                  )}
               </div>
            </div>
         </div>
         {messages?.length ? (
            <ul className='conversation-content' ref={viewRef}>
               {/* <p className='conversation-info'>YESTERDAY</p> */}
               {messages.map((message, index) => (message.user === 'admin' ? (
                     <p key={index} className='conversation-info'>
                        {message.text}
                     </p>
               ) : (
                     <Message
                        key={index}
                        socket={socket}
                        messages={messages}
                        setMessages={setMessages}
                        message={message}
                        isRoom={isRoom}
                        own={
                           message.sender === user._id
                           || message.user === name
                        }
                     />
               )))}
               {userTyping && (
                  <p
                     style={{
                       alignSelf: 'flex-start',
                       marginTop: 'auto',
                     }}
                     className='conversation-info'>
                     Typing ...
                  </p>
               )}
            </ul>
         ) : (
            <ul className='conversation-content'>
               <h3 className='text-muted'>No Messages, Say Hi</h3>
            </ul>
         )}
         <footer>
            {isRoom ? (
               <form className='form'>
                  <div className='form-wrapper'>
                     <div className='left-bar'>
                        <CamSVG />
                        <ImageSVG />
                     </div>
                     <div className='middel-bar'>
                        <input
                           value={message}
                           onChange={(e) => setMessage(e.target.value)
                           }
                           onKeyPress={(e) => (e.key === 'Enter' ? sendMessage(e) : '')
                           }
                           type='text'
                           resize='none'
                           className='input-message'
                           placeholder='Type something here...'
                        />
                     </div>

                     <section className='right-bar'>
                        <button type='submit'>
                           <SendSVG />
                        </button>
                     </section>
                  </div>
               </form>
            ) : (
               <ChatForm
                  messages={messages}
                  setMessages={setMessages}
                  arrivallMessage
                  setArrivalMessage
                  setUnreadMsges={setUnreadMsges}
                  playSoundEffect={playSoundEffect}
               />
            )}
         </footer>
      </section>
  );
}

export default ChatBox;
