import {
  GET_USER,
  UPDATE_USER,
  FOLLOW,
  UNFOLLOW,
  REGISTER,
  LOGOUT,
  SET_LOADING,
} from '../types/action-types';

const initialState = {
  token: JSON.parse(localStorage.getItem('token')),
  isAuthenticated: null,
  isLoading: false,
  user: [] || JSON.parse(localStorage.getItem('user')),
};
const authReducer = (state = initialState, action) => {
  switch (action.type) {
    //   case "LOGIN_START":
    //      return {
    //         user: null,
    //         isFetching: true,
    //         error: false,
    //      };
    case 'LOGIN_SUCCESS':
      return {
        user: action.payload,
        isFetching: false,
        error: false,
      };
      //   case "LOGIN_FAILURE":
      //      return {
      //         user: null,
      //         isFetching: false,
      //         error: true,
      //      };
    case GET_USER:
      return {
        ...state,
        user: action.payload.user,
        isLoading: false,
      };
    case UPDATE_USER:
      return {
        ...state,
        user: action.payload.user,
        isLoading: false,
      };
    case REGISTER:
      return { ...state, user: action.payload };
    case FOLLOW:
      return {
        ...state,
        user: {
          ...state.user,
          followings: [...state.user.followings, action.payload],
        },
      };
    case UNFOLLOW:
      return {
        ...state,
        user: {
          ...state.user,
          followings: state.user.followings.filter(
            (following) => following !== action.payload,
          ),
        },
      };
    case LOGOUT:
      return state;
    case SET_LOADING:
      return {
        ...state,
        isLoading: true,
      };
    default:
      return state;
  }
};
// case "LOGIN_START":
//    return {
//       user: null,
//       isFetching: true,
//       error: false,
//    };
// case "LOGIN_SUCCESS":
//    return {
//       user: action.payload,
//       isFetching: false,
//       error: false,
//    };
// case "LOGIN_FAILURE":
//    return {
//       user: null,
//       isFetching: false,
//       error: true,
//    };
export default authReducer;
