import { authAxios } from '../../services/auth-header';
import {
  GET_USER,
  UPDATE_USER,
  FOLLOWERS,
  FOLLOWING,
  GET_USER_FRIENDS,
  SET_ONLINE_FRIENDS,
  SET_NOTIFICATION,
  SET_LOADING,
  SET_USER_FRIENDS_LOADING,
} from '../types/action-types';
import { returnErrors } from './errorActions';

export const getUser = (userId) => async (dispatch) => {
  try {
    const { data: user } = await authAxios.get(`users?userId=${userId}`);
    dispatch({ type: GET_USER, payload: user });
    return user;
  } catch (error) {
    console.error(error);
  }
};

export const editUser = (userId, body) => async (dispatch) => {
  try {
    const { data: user } = await authAxios.patch(`/users/${userId}`, body);
    dispatch({ type: UPDATE_USER, payload: user });
  } catch (error) {
    dispatch(returnErrors(error.response.data.msg, error.response.status));
  }
};

export const getUserFollowers = (userId) => async (dispatch) => {
  dispatch({ type: SET_LOADING });
  try {
    const { data } = await authAxios.get(`/users/followers/${userId}`);
    dispatch({
      type: FOLLOWERS,
      payload: data.followers,
    });
  } catch (error) {
    console.error(error);
  }
};

export const getUserFollowing = (userId) => async (dispatch) => {
  dispatch({ type: SET_LOADING });
  try {
    const { data } = await authAxios.get(`/users/following/${userId}`);
    dispatch({
      type: FOLLOWING,
      payload: data.following,
    });
  } catch (error) {
    console.error(error);
  }
};

export const getUserFriends = (userId) => async (dispatch) => {
  dispatch({ type: SET_USER_FRIENDS_LOADING });
  try {
    if (userId) {
      const { data } = await authAxios.get(`/users/friends/${userId}`);
      dispatch({ type: GET_USER_FRIENDS, payload: data });
    }
  } catch (error) {
    console.log(error.message);
  }
};

export const setOnlineFriends = (onlineFriends) => async (dispatch) => {
  try {
    dispatch({ type: SET_ONLINE_FRIENDS, payload: onlineFriends });
  } catch (error) {
    console.error(error);
  }
};

export const setNotification = (notification) => async (dispatch) => {
  try {
    dispatch({ type: SET_NOTIFICATION, payload: notification });
  } catch (error) {
    console.error(error);
  }
};
