import {
  useState, useEffect, useRef, useContext,
} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { authAxios } from '../../services/auth-header';

import './chat-box.css';
import { PopupMessagesSkeleton } from '../skeleton/MessagesSkeleton';

import { CamSVG, ImageSVG, SendSVG } from '../../Assets/icons/SVG.js';

import { GlobalContext } from '../../context/GlobalContext';

import useTyping from '../../Hooks/useTyping';

const ChatForm = ({
  arrivallMessage,
  setArrivalMessage,
  setUnreadMsges,
  setMessages,
  messages,
  playSoundEffect,
}) => {
  const { socket } = useContext(GlobalContext);
  const { user } = useSelector((state) => state.auth);
  const { currentConversation } = useSelector(
    (state) => state.conversation,
  );

  const [newMessage, setNewMessage] = useState('');

  const [userTyping, setUserTyping] = useState(false);

  const {
    startTyping,
    isTyping,
    setIsTyping,
    stopTyping,
    cancelTyping,
  } = useTyping();

  const handleStartTyping = () => {
    const reciverId = currentConversation?.members.find(
      (member) => member !== user._id,
    );

    if (!socket) return;
    socket.emit('startTyping', {
      senderId: user._id,
      reciverId,
      typing: true,
    });

    startTyping();
  };

  const handleStopTyping = () => {
    const reciverId = currentConversation?.members?.find(
      (member) => member !== user._id,
    );

    if (!socket) return;
    const timeout = setTimeout(() => {
      stopTyping();
      socket.emit('startTyping', {
        senderId: user._id,
        reciverId,
        typing: false,
      });
    }, 2000);
      // clearTimeout(timeout);
  };

  // Submit New Message
  const handleSendMessage = async (e) => {
    e.preventDefault();
    if (!newMessage) return;

    // message content
    const message = {
      sender: user._id,
      text: newMessage,
      conversationId: currentConversation._id,
    };

    setMessages((prev) => [...prev, message]);
    playSoundEffect('/audio/send-message.mp3');

    // get reciverId
    const reciverId = currentConversation?.members?.find(
      (member) => member !== user._id,
    );
      // Socket SEND MESSAGE
    socket.emit('sendMessage', {
      senderId: user._id,
      reciverId,
      text: newMessage,
      conversation: currentConversation,
    });
    setUnreadMsges(0);
    setNewMessage('');

    // send message to DB
    try {
      await authAxios.post('/messages', message);
    } catch (error) {
      console.error(error);
    }
  };

  return (
      <form className='form' onSubmit={handleSendMessage}>
         <div className='form-wrapper'>
            <div className='left-bar'>
               <CamSVG />
               <ImageSVG />
            </div>
            <div className='middel-bar'>
               <input
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  onKeyPress={handleStartTyping}
                  onKeyUp={handleStopTyping}
                  type='text'
                  resize='none'
                  className='input-message'
                  placeholder='Type something here...'
               />
            </div>

            <section className='right-bar'>
               <button type='submit'>
                  <SendSVG />
               </button>
            </section>
         </div>
      </form>
  );
};

export default ChatForm;
