import { memo } from 'react';

function Caption({ user, caption, captionRef }) {
//   console.log('Caption  rerender');

  return (
      <div className='caption'>
         <p ref={captionRef}>
            <b>{caption && user.displayName}</b> {caption}
            <span className='harsh-tag'>#lifestyle</span>
         </p>
      </div>
  );
}

export default memo(Caption);
