import { useState, useContext } from 'react';
import { useSelector } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { IoColorPaletteOutline } from 'react-icons/io5';
import { AiOutlineBell } from 'react-icons/ai';

import {
  HomeSVG,
  DiscoverySVG,
  NotificationSVG,
  BookmarkSVG,
  SettingSVG,
  JoinRoomSVG,
  MessageSVG,
} from '../../Assets/icons/SVG';

import SidebarLink from './SidebarLink';
import { GlobalContext } from '../../context/GlobalContext';
import NotificationPopup from '../../components/Notification';

AiOutlineBell;

function Sidebar() {
  const { setIsRoomModalOpen } = useContext(GlobalContext);
  const { notifications } = useSelector((state) => state.user);

  const [isOpen, setIsOpen] = useState(false);

  return (
      <div className='sidebar'>
         <SidebarLink text='home' path='/' icon={<HomeSVG />} />
         <SidebarLink
            text='explore'
            path='/explore'
            icon={<DiscoverySVG />}
         />

         <a
            onClick={() => setIsOpen(!isOpen)}
            className={`menu-item ${isOpen && 'active'}`}
            id='notifications'>
            <span className='icon'>
               <NotificationSVG />
               {notifications.length > 0 && (
                  <small className='notification-count'>
                     {notifications.length > 9
                       ? '+9'
                       : notifications.length}
                  </small>
               )}
            </span>
            <h3>Notifications</h3>
            <NotificationPopup />
         </a>
         <a className='menu-item' id='messages-notification'>
            <span>
               <MessageSVG>
                  <small className='notification-count'>6</small>
               </MessageSVG>
            </span>
            <h3>Messagse</h3>
         </a>
         <a className='menu-item' id='theme'>
            <span>
               <IoColorPaletteOutline />
            </span>
            <h3>Theme</h3>
         </a>

         <SidebarLink
            text='bookmarks'
            path='/bookmarks'
            icon={<BookmarkSVG />}
         />
         <a
            onClick={() => setIsRoomModalOpen(true)}
            className='menu-item'
            id='theme'>
            <span>
               <JoinRoomSVG />
            </span>
            <h3>Join Room</h3>
         </a>
         {/* <SidebarLink text='theme' icon={<IoColorPaletteOutline />} /> */}
         <SidebarLink
            text='settings'
            path='/analytics'
            icon={<SettingSVG />}
         />
      </div>
  );
}

export default Sidebar;
