import { useState, useEffect } from 'react';
import { authAxios } from '../../../services/auth-header';

import { ProfileSVG } from '../../../Assets/icons/SVG';

function Conversation({
  conversation,
  currentUser,
  currentConversationId,
}) {
  const [user, setUser] = useState(null);

  useEffect(() => {
    let isSubscribed = true;
    // get friend id
    const friendId = conversation.members.find(
      (m) => m !== currentUser._id,
    );
    const fetchOtherUser = async () => {
      if (friendId) {
        const { data } = await authAxios.get(
          `/users?userId=${friendId}`,
        );
        if (isSubscribed) {
          setUser(data.user);
        }
      }
    };
    fetchOtherUser();
    return () => (isSubscribed = false);
  }, [currentUser, conversation]);

  return (
      <div
         className={`user-friend-card ${
           conversation?._id === currentConversationId && 'active'
         }`}>
         <div className='profile-picture'>
            {user?.profilePicture ? (
               <img alt='profile-picture' src={user.profilePicture} />
            ) : (
               <ProfileSVG />
            )}
         </div>
         <div className='card-info '>
            <h4 className='name'>{user?.displayName}</h4>
         </div>
      </div>
  );
}

export default Conversation;
