import {
  useState, useEffect, useCallback, useContext,
} from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { IoPersonOutline } from 'react-icons/io5';
import { useDispatch, useSelector } from 'react-redux';
import { authAxios } from '../../../services/auth-header';
import SearchSkeleton from '../../../components/skeleton/Search';
import { GlobalContext } from '../../../context/GlobalContext';

function SearchBar() {
  const { socket } = useContext(GlobalContext);
  const {
    _id: userId,
    profilePicture,
    displayName,
    username,
    role,
  } = useSelector((state) => state.auth.user);
  const dispatch = useDispatch();

  const [search, setSearch] = useState('');
  const [usersList, setUsersList] = useState([]);
  const [isLoading, setIsloading] = useState(false);

  const navigate = useNavigate();

  // search user
  const searchUser = useCallback(
    async () => {
      setIsloading(true);
      try {
        const { data: users } = await authAxios.get(
          `/users/search?username=${search}&fields=username,displayName,profilePicture,city,from`,
        );
        setUsersList(users.users);
        setIsloading(false);
      } catch (error) {
        console.error(error);
      }
    },
    [search],
  );

  useEffect(() => {
    if (!search) {
      setUsersList([]);
    } else {
      searchUser();
    }
  }, [search, searchUser]);

  // search with submit
  const handleSearch = async (e) => {
    e.preventDefault();
    if (search) {
      try {
        const { data: users } = await authAxios.get(
          `/users/search?username=${search}&fields=username,displayName,profilePicture,adress`,
        );
        setUsersList(users.users);
        navigate(`/users/search?username=${search}`);
      } catch (error) {
        console.error(error);
      }
    }
  };

  const sendNotification = (actionType, objectName, ref, id) => {
    socket.emit('sendNotification', {
      senderId: userId,
      senderName: displayName,
      senderImg: profilePicture || '',
      receiverId: id,
      time: Date.now(),
      actionType,
      objectName,
      ref: username,
    });
  };

  return (
      <form
         className={`search-bar ${search && 'active'}`}
         onSubmit={handleSearch}>
         <i className='uil uil-search' />
         <input
            onChange={(e) => setSearch(e.target.value)}
            type='search'
            placeholder='Search for users'
         />
         <ul className={`search-list ${isLoading && 'loading'}`}>
            {isLoading && <SearchSkeleton />}
            { usersList.filter((user) => user._id !== userId).map(
              ({
                _id, username, displayName, profilePicture,
              }) => (
                  <Link
                     key={_id}
                     to={`/profile/${username}`}
                     className='item'
                     onClick={() => {
                       setSearch('');
                       role !== 'admin' && sendNotification('view-profile', 'profile', username, _id);
                     }}>
                     <div className='image-container'>
                        {profilePicture ? (
                           <img
                              width='40px'
                              height='40px '
                              src={profilePicture}
                              alt='user image'
                           />
                        ) : (
                           <IoPersonOutline />
                        )}
                     </div>
                     <div className='info'>
                        <h3 className='username'>{displayName}</h3>
                        <p className='caption'>
                           <span className='status'>Friend</span>
                           <span className='region'>
                              {/* {city || country || ""} */}
                           </span>
                        </p>
                     </div>
                  </Link>
              ),
            )}
         </ul>
      </form>
  );
}

export default SearchBar;
