import {
  useEffect,
  useState,
  useRef,
} from 'react';

import {
  IoVideocamOutline,
  IoImageOutline,
  IoAddCircleOutline,
  IoAttachOutline,
  IoHappyOutline,
  IoHeartOutline,
  IoCallOutline,
  IoEllipsisHorizontalCircleOutline,
} from 'react-icons/io5';

import { authAxios } from '../../services/auth-header';
import Message from './message/Message';

import './style.css';

function ChatBox({ user, currentChat, socket }) {
  const [friendConv, setFriendConv] = useState(null);
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const viewRef = useRef(null);

  // send  new message
  const handleSendMessage = async (e) => {
    e.preventDefault();
    if (!newMessage) return;
    const message = {
      sender: user._id,
      text: newMessage,
      conversationId: currentChat._id,
    };

    const reciverId = currentChat.members.find(
      (member) => member !== user._id,
    );

    socket.emit('sendMessage', {
      senderId: user._id,
      reciverId,
      text: newMessage,
    });

    try {
      const { data } = await authAxios.post('/messages', message);
      setMessages([...messages, data.message]);
      setNewMessage('');
    } catch (error) {
      console.error(error);
    }
  };

  // scroll to viwe when send message
  useEffect(() => {
    viewRef?.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  // get conversation messages
  useEffect(() => {
    // messanger in conversation
    const getMessages = async () => {
      try {
        const { data } = await authAxios.get(
          `/messages/${currentChat?._id}`,
        );
        setMessages(data.messages);
      } catch (error) {
        console.error(error);
      }
    };
      // get frind in choosen conversation
    const friendId = currentChat?.members.find(
      (m) => m !== user._id,
    );

    const fetchOtherUser = async () => {
      try {
        const { data } = await authAxios.get(
          `/users?userId=${friendId}`,
        );
        setFriendConv(data.user);
      } catch (error) {
        console.error(error);
      }
    };
    getMessages();
    fetchOtherUser();
  }, [currentChat, user._id]);

  return (
      <div className='chat-box'>
         <nav className='navbar'>
            <div className='navbar-container'>
               <div className='left-bar'>
                  <div className='user'>
                     <div className='image-container'>
                        <img
                           alt={`${friendConv?.displayName}Profile Image`}
                           src={friendConv?.profilePicture}
                        />
                     </div>
                     <h4 className='username'>
                        {friendConv?.username}
                     </h4>
                  </div>
               </div>
               <div className='right-bar'>
                  <div className='icons'>
                     <IoVideocamOutline />
                     <IoCallOutline />
                     <IoEllipsisHorizontalCircleOutline />
                  </div>
               </div>
            </div>
         </nav>
         <div className='chat-box-wrapper'>
            {messages.length > 0 ? (
              messages.map((message) => (
                  <div key={message._id}>
                     <Message
                        message={message}
                        currentUser={user}
                        own={message.sender === user._id}
                     />
                  </div>
              ))
            ) : (
               <div className='no-messages'>
                  <span>No Messages ...</span>
                  <p>Say hi</p>
                  <p className='msg'>
                     this app alpha version made it By Amin benz for
                     testing purpose
                  </p>
               </div>
            )}
         </div>
         <form
            className='chat-form'
            onSubmit={(e) => handleSendMessage(e)}>
            <div className='chat-form-wrapper'>
               <div className='left-bar'>
                  <IoVideocamOutline />
                  <IoImageOutline />
                  <IoAddCircleOutline />
                  <IoAttachOutline />
               </div>
               <div className='middel-bar'>
                  <input
                     value={newMessage}
                     onChange={(e) => setNewMessage(e.target.value)}
                     type='text'
                     resize='none'
                     className='input-message'
                     placeholder='Type something here...'
                  />
               </div>
               <div className='right-bar'>
                  <IoHappyOutline />
                  <IoHeartOutline />
               </div>
            </div>
         </form>
      </div>
  );
}

export default ChatBox;
