import React, { useEffect, useState, useContext } from 'react';

import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import {
  getUserFriends,
  setOnlineFriends,
} from '../../Redux/actions/userActions';
import { GlobalContext } from '../../context/GlobalContext';
import { ProfileSVG } from '../../Assets/icons/SVG';
import Friend from './Friend';
import FriendsSkeleton from '../skeleton/FriendsSkeleton';

function UserFriends({ title }) {
  const { socket } = useContext(GlobalContext);
  const { user } = useSelector((state) => state.auth);
  const { friends, onlineFriends, isUserFriendsLoading } = useSelector(
    (state) => state.user,
  );

  const [conversations, setConversations] = useState([]);
  const [currentChat, setCurrentChat] = useState();
  const [onlineUsers, setOnlineUsers] = useState([]);

  const dispatch = useDispatch();

  // get userFriends
  useEffect(() => {
    let isFetching = true;

    socket.emit('addUser', user._id);
    socket.on('getUsers', (users) => {
      if (isFetching) {
        setOnlineUsers(
          user?.followings?.filter((f) => users.some((u) => u.userId === f)),
        );
      }
    });
    return () => {
      isFetching = false;
    };
  }, [socket, user]);

  // get userFriends
  useEffect(() => {
    dispatch(getUserFriends(user._id));
  }, [dispatch, user._id]);

  // get only active friends
  useEffect(() => {
    const friendOnline = friends?.filter((friend) => onlineUsers?.includes(friend._id));
    //  dispatch(
    //    setOnlineFriends(

    //    )
    //  );
  }, [friends, onlineUsers]);

  if (isUserFriendsLoading) {
    return (
      <FriendsSkeleton/>
    );
  }

  return (
      <div className='friend-requests'>
         <h4>{title}</h4>
         {friends.map((userFriend) => (
           <Friend key={userFriend._id} userFriend={userFriend}/>
         ))}
      </div>
  );
}

export default UserFriends;
