import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { authAxios } from '../../services/auth-header';
import Feed from '../../components/Feed';
import CreatePost from '../../components/CreatePost';
import User from '../../Pages/profile/User';
import { getUserFriends } from '../../Redux/actions/userActions';

function MainContent() {
  const username = useParams().id;
  const [user, setUser] = useState([]);
  const currentUser = useSelector((state) => state.auth.user);
  const dispatch = useDispatch();
  // GET User Profile
  useEffect(() => {
    let isFetching = true;
    const fetchUser = async () => {
      try {
        const { data } = await authAxios.get(`/users?username=${username}`);

        if (isFetching) {
          setUser(data.user);
        }
      } catch (error) {
        console.error(error);
      }
    };
    fetchUser();
    return () => {
      isFetching = false;
    };
  }, [username]);

  // Get userFriends
  //   useEffect(() => {
  //     dispatch(getUserFriends(user._id));
  //   }, [user._id]);

  return (
    <div className="middle">
      <User />
      {currentUser.username === username && <CreatePost />}
      <Feed username={username} />
    </div>
  );
}

export default MainContent;
