import {
  FOLLOWERS,
  FOLLOWING,
  SET_ONLINE_FRIENDS,
  GET_USER_FRIENDS,
  SET_NOTIFICATION,
  SET_LOADING,
  SET_USER_FRIENDS_LOADING,
} from '../types/action-types';

const initialState = {
  followers: [],
  following: [],
  friends: [],
  onlineFriends: [],
  notifications: [],
  isLoading: false,
  isUserFriendsLoading: true,

};

const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOWERS:
      FOLLOWING;
      return {
        ...state,
        followers: action.payload,
        isLoading: false,
      };
    case GET_USER_FRIENDS:
      return {
        ...state,
        friends: action.payload,
        isUserFriendsLoading: false,
      };
    case SET_ONLINE_FRIENDS:
      return {
        ...state,
        onlineFriends: action.payload,
        isLoading: false,
      };
    case SET_NOTIFICATION:
      return {
        ...state,
        notifications: [...state.notifications, action.payload],
        isLoading: false,
      };
    case SET_USER_FRIENDS_LOADING:
      return { ...state, isUserFriendsLoading: true };
    case SET_LOADING:
      return { ...state, isLoading: true };
    default:
      return state;
  }
};

export default userReducer;
