import {
  useState, useEffect, useContext, useCallback,
} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import ReactEmoji from 'react-emoji';
import { authAxios } from '../../services/auth-header';

import { ProfileSVG } from '../../Assets/icons/SVG';
import { setCurrentConversation } from '../../Redux/actions/conversationActions';
import { GlobalContext } from '../../context/GlobalContext';

function Conversation({ conversation, currentUser }) {
  const { setIsChatBoxPopupOpen } = useContext(GlobalContext);
  const [user, setUser] = useState(null);
  const [lastMsg, setLastMsg] = useState(null);

  const dispatch = useDispatch();
  const { onlineFriends } = useSelector((state) => state.user);

  useEffect(() => {
    let isSubscribed = true;
    const friendId = conversation.members.find(
      (member) => member !== currentUser._id,
    );
    const getUser = async () => {
      try {
        const { data } = await authAxios.get(`/users?userId=${friendId}`);
        if (isSubscribed) {
          setUser(data.user);
        }
      } catch (error) {
        console.error(error);
      }
    };

    getUser();

    const getMessages = async () => {
      try {
        const { data } = await authAxios.get(`/messages/${conversation._id}`);
        const lastMessage = data.messages[data.messages.length - 1];
        if (isSubscribed) {
          setLastMsg(lastMessage);
        }
      } catch (error) {
        console.error(error);
      }
    };
    getMessages();
    return () => (isSubscribed = false);
  }, [conversation._id, conversation.members, currentUser]);

  const isMeTheSender = useCallback(
    () => {
      if (lastMsg) {
        if (lastMsg.sender === currentUser._id) {
          return true;
        }
        return false;
      }
    },
    [currentUser._id, lastMsg],
  );

  useEffect(() => {
    isMeTheSender();
  }, [lastMsg, currentUser, isMeTheSender]);

  const handleCurrentConversation = (conversation) => {
    dispatch(setCurrentConversation(conversation));
    setIsChatBoxPopupOpen(true);
  };

  return (
    <>
      {lastMsg?.text && (
        <div
          onClick={(e) => handleCurrentConversation(conversation)}
          className="message"
        >
          <div className="profile-photo">
            {user?.profilePicture ? (
              <img
                src={user.profilePicture}
                alt={`${user.displayName} Profile Image`}
              />
            ) : (
              <ProfileSVG />
            )}
            <div
              className={`offline ${onlineFriends.map(
                (friend) => friend?._id === user?._id && 'online',
              )}`}
            />
          </div>
          <div className="message-body">
            <h5 className="username">{user?.displayName}</h5>
            <p className={isMeTheSender() ? 'text-muted' : 'text-bold'}>
              {isMeTheSender() && 'You : '}
              {ReactEmoji.emojify(lastMsg?.text)}
            </p>
          </div>

        </div>

      )}      {/*
         <div className='message'>
      <div className='profile-photo'>
         <img src='./images/people/profile-12.jpg' />
         <div className='active' />
      </div>
      <div className='message-body'>
         <h5>Catarina</h5>
         <p className='text-bold'>Received bruh. Thanks!</p>
      </div>
     </div>
        <div className='message'>
            <div className='profile-photo'>
               <img src='./images/people/profile-3.jpg' />
               <div className='active' />
            </div>
            <div className='message-body'>
               <h5>Franca Deila</h5>
               <p className='text-bold'>Received bruh. Thanks!</p>
            </div>
         </div>
         <div className='message'>
            <div className='profile-photo'>
               <img src='./images/people/profile-5.jpg' />
            </div>
            <div className='message-body'>
               <h5>Daniella Jackson</h5>
               <p className='text-bold'>2 new messages</p>
            </div>
         </div>
         <div className='message'>
            <div className='profile-photo'>
               <img src='./images/people/profile-6.jpg' />
            </div>
            <div className='message-body'>
               <h5>Juliet Makarey</h5>
               <p className='text-muted'>lol u right</p>
            </div>
         </div>
         <div className='message'>
            <div className='profile-photo'>
               <img src='./images/people/profile-7.jpg' />
               <div className='active' />
            </div>
            <div className='message-body'>
               <h5>Chantel Msiza</h5>
               <p className='text-bold'>Birthday Tomorrow!</p>
            </div>
         </div> */}
    </>
  );
}

export default Conversation;
