import { useEffect, useState, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';

import { IoCloseOutline } from 'react-icons/io5';
import { format } from 'timeago.js';
import { getUser, editUser } from '../../Redux/actions/userActions';
import { CamSVG } from '../../Assets/icons/SVG';
import { logout } from '../../Redux/actions/authActions';
import Switch from '../../components/Switch';

function EditUserModal({
  isEditUserModalOpen,
  setIsEditUserModalOpen,
}) {
  const dispatch = useDispatch();
  const [user, setUser] = useState(
    useSelector((state) => state.auth.user),
  );
  const navigate = useNavigate();
  const error = useSelector((state) => state.error);
  // get neded user info
  const {
    coverPicture,
    profilePicture,
    displayName,
    about,
    location,
    links,
  } = user;

  // set userInfo in values
  const [values, setValues] = useState({
    coverPicture,
    profilePicture,
    displayName,
    about,
    location,
    website: links?.website,
  });

  const inputs = [
    {
      id: 1,
      name: 'displayName',
      type: 'text',
      placeholder: 'Name',
      label: 'Name',
      errormessage: 'Username should be 3-16 charachters !',
      // pattern: "^[A-Za-z0-9]{3,10}$",
      required: true,
      minLength: '3',
      maxLength: '16',
      hidden: false,
    },
    {
      id: 2,
      name: 'about',
      type: 'text',
      placeholder: 'Bio',
      label: 'Bio',
      required: false,
      errormessage: 'name input not valid',
      hidden: false,
    },
    {
      id: 6,
      name: 'website',
      type: 'text',
      placeholder: 'Website',
      label: 'Website',
      required: false,
      errormessage: 'name input not valid',
      hidden: false,
    },
    // {
    //    id: 4,
    //    name: "location",
    //    type: "text",
    //    placeholder: "Location",
    //    label: "Location",
    //    required: false,
    //    errormessage: "name input not valid",
    //    patren: "",
    //    hidden: false,
    // },
  ];

  // get user
  useEffect(() => {
    dispatch(getUser(user._id));
  }, [user._id, dispatch]);

  // form data
  // const [values, setValues] = useState(null);

  const onChange = (e) => {
    setValues({ ...values, [e.target.name]: e.target.value });
  };

  const handleEditUserSubmit = (e) => {
    e.preventDefault();
    const newData = {};
    if (user.coverPicture !== values.coverPicture) {
      newData.coverPicture = values.coverPicture;
    }
    if (user.profilePicture !== values.profilePicture) {
      newData.profilePicture = values.profilePicture;
    }
    if (user.displayName !== values.displayName) {
      newData.displayName = values.displayName;
    }
    if (user.about !== values.about) {
      newData.about = values.about;
    }
    if (user.location !== values.location) {
      newData.location = values.location;
    }
    dispatch(editUser(user._id, newData));
    setIsEditUserModalOpen(false);
  };

  // references form
  const profilePicInputRef = useRef(null);
  const coverPicInputRef = useRef(null);

  // file
  function getBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (err) => reject(err);
    });
  }

  const inputImage = async (e) => {
    const file = e.target.files[0];
    const base46 = await getBase64(file);
    setValues({ ...values, [e.target.name]: base46 });
  };

  // click outside form modal
  const handleClickOutsideModal = (e) => {
    const target = e.target.classList.contains('edit-user-modal');
    if (target) {
      setUser(user);
      setIsEditUserModalOpen(false);
    }
  };

  return (
    <section
      onClick={(e) => handleClickOutsideModal(e)}
      className={`edit-user-modal ${
        isEditUserModalOpen && 'open'
      }`}
    >
      {error && <div className="error">{error.msg}</div>}
      <form className="" onSubmit={(e) => handleEditUserSubmit(e)}>
        <div className="header">
          <IoCloseOutline
            onClick={() => setIsEditUserModalOpen(false)}
          />
          <h4 className="title">Edit Profile</h4>
          <button type="submit" className="btn btn-primary">
            Save
          </button>
        </div>
        <div
          className="edit cover-picture "
          onClick={() => coverPicInputRef.current.click()}
        >
          <CamSVG />
          <input
            ref={coverPicInputRef}
            name="coverPicture"
            onChange={inputImage}
            type="file"
            hidden
          />
          {values?.coverPicture && (
            <img
              src={values?.coverPicture}
              alt="cover picture"
            />
          )}
        </div>
        <div
          className="edit profile-picture"
          onClick={() => profilePicInputRef.current.click()}
        >
          <CamSVG />
          <input
            ref={profilePicInputRef}
            name="profilePicture"
            onChange={inputImage}
            type="file"
            hidden
          />
          {values?.profilePicture && (
            <img
              src={values?.profilePicture}
              alt="profile picture"
            />
          )}
        </div>
        <ul className="input-list">
          {inputs.map(({ ...input }) => (
            <div key={input.id}>
              <li className={input.name}>
                <label htmlFor={input.name}>
                  {input.label}
                </label>
                <input
                  onChange={onChange}
                  defaultValue={values[input.name]}
                  {...input}
                />
                <span className="error-message">
                  {input.errormessage}
                </span>
              </li>
            </div>
          ))}
          {/* <li className='location'>
                  <label htmlFor='location'>Location</label>
                  <input
                     defaultValue={values.location.map(
                        ({ city, from }) => `${city} , ${from}`
                     )}
                     name='city'
                     type='text'
                  />
               </li> */}

          <div className="join-in">
            <p>
              Joined At
              {' '}
              <span>{format(user.createdAt, 'twitter')}</span>
            </p>
            <Switch/>
            <div
              style={{ backgroundColor: 'red', color: 'white', marginTop: '20px' }}
              className="btn btn-danger"
              onClick={() => dispatch(logout(navigate))}
            >
              Log out
            </div>
          </div>
        </ul>
      </form>
    </section>
  );
}

export default EditUserModal;
