const converFilesToFileBase = (files, post, setPost) => {
  let isFilesReady = true;
  let myFiles = [];

  const filePromises = Object.entries(files).map((item) => new Promise((resolve, reject) => {
    const file = item[1];
    const reader = new FileReader();
    reader.readAsBinaryString(file);

    reader.onload = (event) => {
      // if it's multiple upload field then set the object key as picture[0], picture[1]
      // otherwise just use picture
      // Convert Base64 to data URI
      // Assign it to your object
      myFiles = [...myFiles, { imgUrl: `data:${file.type};base64,${btoa(event.target.result)}`, createdAt: Date.now() }];

      resolve();
    };
    reader.onerror = () => {
      reject();
    };
  }));

  Promise.all(filePromises)
    .then(() => {
      isFilesReady = true;
      setPost({
        ...post,
        images: myFiles,
      });
    })
    .catch((error) => {
      console.error(error);
    });
};

export default converFilesToFileBase;
