import {
  useState, useEffect, useCallback, memo,
} from 'react';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import { ProfileSVG } from '../../../Assets/icons/SVG';
import { authAxios } from '../../../services/auth-header';

function LikedBy({
  likes, userId, setIsLiked, randomUser,
}) {
  // const [isLiked, setIsLiked] = useState(false);
  const [usersLikeThisPost, setUsersLikeThisPost] = useState([]);

  // get users liked this post
  useEffect(() => {
    let isSubscribed = true;
    const fetch = async () => {
      const usersLikePost = await Promise.all(
        likes.map(
          async (id) => await authAxios
            .get(`/users?userId=${id}`)
            .then((res) => res.data.user),
        ),
      );
      if (isSubscribed) {
        setUsersLikeThisPost(usersLikePost);
      }
    };
    fetch();
    return () => (isSubscribed = false);
  }, [likes]);

  // is currnet user Like this Post >
  useEffect(() => {
    setIsLiked(likes.includes(userId));
  }, [userId, likes, setIsLiked]);

  // like ui logice

  const handleLikes = useCallback(() => {
    if (likes.length === 1) {
      return (
            <p>
               Liked by
               <b>
                  {likes.includes(userId)
                    ? ' You '
                    : ` ${randomUser?.displayName} `}
               </b>
            </p>
      );
    }
    // many users like this postlikes
    if (likes.length > 1) {
      return (
            <div>
               Liked by
               <Link to={`/profile/${randomUser?.username}`}>
                  <b> {randomUser?.displayName} </b>
               </Link>
               and
               <b> {likes.length - 1} others</b>
            </div>
      );
    }

    return <div />;
  }, [likes, randomUser, userId]);

  useEffect(() => {
    handleLikes();
  }, [likes.length, handleLikes]);

  const [list, setList] = useState(false);

  // useEffect(() => {
  //   console.log('liked by buttons rerender ');
  // }, []);

  return (
      <div className='liked-by'>
         <div
            className='avatars'
            onMouseOver={() => setList(true)}
            onMouseLeave={() => setList(false)}>
            <ul className={`users-list ${list && 'show'}`}>
               {usersLikeThisPost.map((user) => (
                  <li key={user._id}>{user.displayName}</li>
               ))}
            </ul>
            {likes.length
              ? usersLikeThisPost.map((user) => (
                    <span key={user._id}>
                       {user.profilePicture ? (
                          <img
                             src={user.profilePicture}
                             alt={user.profilePicture}
                          />
                       ) : (
                          <ProfileSVG />
                       )}
                    </span>
              ))
              : ''}
         </div>
         {handleLikes()}
      </div>
  );
}

export default memo(LikedBy);
