import {
  CREATE_CONVERSATION,
  GET_CONVERSATIONS,
  GET_CONVERSATION_MESSAGES,
  ADD_CONVERSATION,
  LOAD_CONVERSATIONS,
  REMOVE_MESSGAE,
  SET_CURRENT_CONVERSATION,
} from '../types/action-types';
import { authAxios } from '../../services/auth-header';

export const createConversation = (userId, reciverId) => async (dispatch) => {
  try {
    const { data: conversation } = await authAxios.post(
      `/conversations/${userId}`,
      { senderId: userId, reciverId },
    );

    dispatch({
      type: SET_CURRENT_CONVERSATION,
      payload: conversation.conversation,
    });
  } catch (error) {
    console.error(error);
  }
};

export const getConversations = (userId) => async (dispatch) => {
  dispatch({
    type: LOAD_CONVERSATIONS,
  });
  try {
    const { data } = await authAxios.get(`/conversations/${userId}`);
    dispatch({
      type: GET_CONVERSATIONS,
      payload: data.conversations,
    });
  } catch (error) {
    console.error(error);
  }
};

export const getConversationMessages = (conversationId) => async (dispatch) => {
  try {
    const { data } = await authAxios.get(`/messages/${conversationId}`);
    dispatch({
      type: GET_CONVERSATION_MESSAGES,
      payload: data.messages,
    });
    return data.messages;
  } catch (error) {
    console.error(error);
  }
};

export const setCurrentConversation = (conversation) => async (dispatch) => {
  dispatch({
    type: SET_CURRENT_CONVERSATION,
    payload: conversation,
  });
};

export const removeMessage = (messageId) => async (dispatch) => {
  try {
    await authAxios.delete(`/messages/${messageId}/delete`);
    dispatch({ type: REMOVE_MESSGAE });
  } catch (error) {
    console.error(error);
  }
};
