import React, {
  useEffect, useRef, useState, useContext,
} from 'react';
import { useSelector } from 'react-redux';
import { IoChatbubblesOutline } from 'react-icons/io5';

import './style.css';
import { useNavigate } from 'react-router-dom';
import { GlobalContext } from '../../context/GlobalContext';

const CreateRoom = () => {
  const navigate = useNavigate();
  const { username, displayName } = useSelector(
    (state) => state.auth.user,
  );
  const { setIsRoomModalOpen, isRoomModalOpen } = useContext(GlobalContext);
  const [name, setName] = useState('unknow');
  const [room, setRoom] = useState('global');

  useEffect(() => {
    displayName && setName(displayName);
  }, [displayName]);

  // click outside form modal
  const handleClickOutsideModal = (e) => {
    const target = e.target.classList.contains('alert-card');
    if (target) return setIsRoomModalOpen(false);
  };

  const createRoom = (e) => {
    e.preventDefault();
    navigate(`/?name=${name.trim()}&room=${room}`);
    setIsRoomModalOpen(false);
  };

  const inputRef = useRef(null);

  useEffect(() => {
    if (inputRef && inputRef.current && isRoomModalOpen) {
      inputRef.current.focus();
    }
  }, [isRoomModalOpen]);

  return (
      <section
         className={`alert-card ${isRoomModalOpen && 'active'} `}
         onClick={(e) => handleClickOutsideModal(e)}>
         <div
            className={'alert-card-container'}
            style={{ height: '26rem' }}>
            <div>
               <IoChatbubblesOutline />
            </div>
            <h2> Join Room </h2>
            <p className='caption' style={{ fontSize: '14px' }}>
               this is the name other people in room see You can
               changet it i you want
            </p>
            <form onSubmit={(e) => createRoom(e)}>
               <div className='form-container'>
                  <label htmlFor='name'>
                     Name
                     <input
                        ref={inputRef}
                        autoFocus
                        placeholder='Enter Your Name'
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        id='name'
                        required
                        minLength={3}
                        maxLength={20}
                     />
                  </label>
                  <label htmlFor='room'>
                     Room
                     <input
                        placeholder="What Room Dou you wan't to Join"
                        value={room}
                        onChange={(e) => setRoom(e.target.value)}
                        id='room'
                        required
                        minLength={2}
                        maxLength={20}
                     />
                  </label>
               </div>
               <div className='btns'>
                  <button
                     className='btn btn-secondary'
                     onClick={() => setIsRoomModalOpen(false)}>
                     Cancel
                  </button>
                  <button type='submit' className='btn btn-primary'>
                     Join Room
                  </button>
               </div>
            </form>
         </div>
      </section>
  );
};

export default CreateRoom;
