import { useEffect, useContext, memo } from 'react';
import { IoHeart } from 'react-icons/io5';
import { FiShare2 } from 'react-icons/fi';
import {
  HeartSVG,
  ChatSVG,
  BookmarkSVG,
} from '../../../Assets/icons/SVG';
import { GlobalContext } from '../../../context/GlobalContext';

function ActionButtons({ postId, handleLikeBtn, isLiked }) {
  const { setCurrentId, setIsComment, setIsModalOpen } = useContext(GlobalContext);
  // const dispatch = useDispatch()

  const handleCommentBtn = () => {
    setCurrentId(postId);
    setIsComment(true);
    setIsModalOpen(true);
  };

  //   console.log('action buttons rerender');

  // numbers after first params is a type of notification
  return (
      <div className='action-buttons'>
         <div className='interaction-buttons'>
            <span
               className='icons reactions love-icon'
               onClick={() => (isLiked
                 ? handleLikeBtn('dislike', 'post', postId)
                 : handleLikeBtn('like', 'post', postId))
               }>
               {isLiked ? <IoHeart color='#f91880' /> : <HeartSVG />}
            </span>
            <span
               className='comment-icon'
               onClick={() => handleCommentBtn('comment', 'post')}>
               <ChatSVG />
            </span>
            <span className='share-icon'>
               <FiShare2 />
            </span>
            <div className='bookmark'>
               <span className='bookmark-icon'>
                  <BookmarkSVG />
               </span>
            </div>
         </div>
      </div>
  );
}

export default memo(ActionButtons);
