import { useDispatch, useSelector } from 'react-redux';
import { format } from 'timeago.js';
import { useCallback, useContext, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { ProfileSVG } from '../../Assets/icons/SVG';
import { setNotification } from '../../Redux/actions/userActions';
import { GlobalContext } from '../../context/GlobalContext';

function NotificationPopup() {
  const dispatch = useDispatch();
  const { notifications } = useSelector((state) => state.user);
  const { socket } = useContext(GlobalContext);

  // ki tjik new notification hetha wala ki tetbadel notification state jib
  //   e5er notfy w display in left bottom corner with set time out and boom

  const singleNotification = (
    {
      senderName, senderImg, actionType, objectName, ref, time,
    },
    index,
  ) => {
    let action;
    switch (actionType) {
      case 'like':
        action = `Liked Your ${objectName}`;
        break;
      case 'dislike':
        action = `Dislikd Your ${objectName}`;
        break;
      case 'comment':
        action = `Commented on Your ${objectName}`;
        break;

      case 'share':
        action = `Shared on Your ${objectName}`;
        break;
      case 'view-profile':
        action = `Viewed Your Profile ${objectName}`;
        break;
      case 'follow':
        action = 'Followed you';
        break;
      case 'unfollow':
        action = 'Unfollowed you ';
        break;
      default:
        return action;
    }

    return (
         <div key={index}>
            {senderImg ? (
               <div className='profile-photo'>
                  <img src={senderImg} />
               </div>
            ) : (
               <ProfileSVG />
            )}
            <Link to={`/${objectName}/${ref}`} className='notification-body'>
               <b>{senderName}</b> {action}
               <small className='text-muted'>{format(time)}</small>
            </Link>
         </div>
    );
  };

  const getNotification = useCallback(
    () => {
      socket.on('getNotification', (data) => {
        dispatch(setNotification(data));
      });
    },
    [socket, dispatch],
  );

  useEffect(() => {
    getNotification();
  }, [socket, getNotification]);

  return (
      <div className='notifications-popup'>
         {notifications.length > 0 ? (
           notifications.map((notification, index) => singleNotification(notification, index))
         ) : (
            <div>Your Notification section is Empty</div>
         )}
      </div>
  );
}

