import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { authAxios } from '../../../services/auth-header';

function Message({ message, own, currentUser }) {
  const [sender, setSender] = useState(null);
  useEffect(() => {
    const fetchUser = async () => {
      try {
        const { data } = await authAxios.get(
          `/users?userId=${message.sender}`,
        );
        setSender(data.user);
      } catch (error) {
        console.error(error);
      }
    };
    fetchUser();
  }, [currentUser, message]);

  return (
    <div
      className={
            own ? 'message-contianer own' : 'message-contianer'
         }
    >
      <Link
        to={`profile/${sender?.username}`}
        className="image-container"
      >
        <img src={sender?.profilePicture} alt="user image" />
      </Link>
      <div className="message">
        <span className="text-message">{message.text}</span>
      </div>
    </div>
  );
}

export default Message;
