import React, { useEffect, useMemo, useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import PostDetails from '../../../Pages/PostDetails/index';

const Images = ({
  post,
  user,
  images,
  videosUrl,
  handleLikeBtn,
  isLiked,
}) => {
  const [imagesLength, setImagesLength] = useState('one');
  // post details
  const [itemIndex, setItemIndex] = useState(null);
  const [openPostDetails, setOpenPostDetails] = useState(false);

  const extensions = ['jpg', 'jpeg', 'png', 'gif'];
  // marge images and videos
  let files = useMemo(() => [], []);
  if (images && images[0]) files = [...files, ...images];
  if (videosUrl && videosUrl[0]) files = [...files, ...videosUrl];

  const navigate = useNavigate();
  const location = useLocation();

  // calc files length & set meaning class in parrent elems =>
  useEffect(() => {
    if (files) {
      if (files.length > 4) {
        setImagesLength('many');
      }
      switch (files.length) {
        case 1:
          return setImagesLength('one');
        case 2:
          return setImagesLength('two');
        case 3:
          return setImagesLength('three');
        case 4:
          return setImagesLength('four');
      }
    }
  }, [files]);

  // calc files length & set meaning class in parrent elems =>
  useEffect(() => {
    if (files) {
      if (files.length > 4) {
        setImagesLength('many');
      }
      switch (files.length) {
        case 1:
          return setImagesLength('one');
        case 2:
          return setImagesLength('two');
        case 3:
          return setImagesLength('three');
        case 4:
          return setImagesLength('four');
      }
    }
  }, [files, files.length]);

  // useEffect(() => {
  //   console.log('imagesGrid call');
  // }, []);

  const showPostDetails = (index) => {
    setItemIndex(index);
    setOpenPostDetails(true);
    // const path = location.pathname;
    // navigate(`/post/${post._id}`);
  };

  return (
      // <FilesPreview files={files}></FilesPreview>
      <div className={`mediafiles-preview ${imagesLength}`}>
         {files.map(({ imgUrl, videoUrl }, index) => (
            <div
               key={index}
               onClick={() => {
                 showPostDetails(index);
               }}
               data-count-files={`+${files.length - 4}`}
               className='media-preview'>
               {/* Check BLOB URL If Video Or Image */}
               {extensions.includes(
                 imgUrl.substring(imgUrl.lastIndexOf('.') + 1),
               ) || imgUrl.startsWith('data:image') ? (
                  <img src={imgUrl} alt='Post image' />
                 ) : (
                  <video
                     autoPlay
                     loop
                     controls
                     src={imgUrl}
                     alt='Post Video'
                  />
                 )}
            </div>
         ))}
         {openPostDetails && (
            <PostDetails
               post={post}
               user={user}
               itemIndex={itemIndex}
               mediaFiles={files}
               setOpenPostDetails={setOpenPostDetails}
               handleLikeBtn={handleLikeBtn}
               isLiked={isLiked}
            />
         )}
      </div>
  );
};

export default React.memo(Images);
