import {
  useState, useEffect, useRef, memo,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Picker } from 'emoji-mart';
import { AiOutlineSmile } from 'react-icons/ai';
import {
  BookmarkSVG,
  ChatSVG,
  HeartSVG,
  SendSVG,
  StarSVG,
} from '../../../Assets/icons/SVG';

import { commentPost } from '../../../Redux/actions/postActions';
import SingleComment from './SingleComment';
import './CommentsSection.css';

function CommentSection({ postId, comments }) {
  const { user } = useSelector((state) => state.auth);
  const dispatch = useDispatch();
  const [comment, setComment] = useState('');
  const [showComments, setShowComments] = useState(false);
  const [isCommentsFetch, setIsCommentsFetch] = useState(false);
  const [showEmojis, setShowEmojis] = useState(false);

  const commentsRef = useRef(null);
  const commentsContainerRef = useRef(null);
  const writeCommentRef = useRef(null);

  // soon users in comment

  // submit Commit
  const handleComment = async (e) => {
    e.preventDefault();
    const finalComment = {
      username: user.displayName,
      body: comment.trim(),
      createdBy: user._id,
    };
    if (comment.trim()) {
      dispatch(commentPost(finalComment, postId));
      setComment('');
    }
  };

  useEffect(() => {
    const commentsHeight = commentsRef?.current?.getBoundingClientRect()?.height;
    const writeCommentHeight = writeCommentRef?.current?.getBoundingClientRect()?.height;
    if (showComments && comments.length) {
      commentsContainerRef.current.style.height = `${
        commentsHeight + writeCommentHeight + 15
      }px`;
    } else if (commentsContainerRef.current) {
      commentsContainerRef.current.style.height = '0px';
    }
  }, [showComments, comments]);

  const handleIsCommentsfetch = () => {
    if (isCommentsFetch) {
      setIsCommentsFetch(isCommentsFetch);
    } else {
      setIsCommentsFetch(true);
    }
  };

  // show &Hide comments
  const toggleComments = () => {
    setShowComments(!showComments);
    handleIsCommentsfetch();
  };

  const addEmoji = (e) => {
    const sym = e.unified.split('-');
    const codesArray = [];
    sym.forEach((el) => codesArray.push(`0x${el}`));
    const emoji = String.fromCodePoint(...codesArray);
    // concat string with emojie
    setComment((prev) => prev + emoji);
  };

  //  console.log('comments section rerender');

  return (
      <div className='comments'>
         {comments.length ? (
            <div className='comments-container'>
               <span
                  className='view-more-comments text-muted'
                  onClick={toggleComments}>
                  {showComments ? ' Hide ' : 'show'} all
                  {comments.length} comments
               </span>
               <div
                  ref={commentsContainerRef}
                  className={`comments-content-container ${
                    showComments && 'show'
                  }`}>
                  <ul className='comments-content' ref={commentsRef}>
                     {comments.map((comment) => (
                        <SingleComment
                           isCommentsFetch={isCommentsFetch}
                           key={comment._id}
                           comment={comment}
                           postId={postId}
                        />
                     ))}
                  </ul>

                  <div
                     className='write-comment'
                     ref={writeCommentRef}>
                     <div className='write-comment-container'>
                        <form
                           className='add-comment active bg-color-secondary'
                           action=''
                           onSubmit={handleComment}>
                           <div className='user'>
                              <div className='image-container'>
                                 <img
                                    src={user.profilePicture || ''}
                                    alt='user profile image'
                                 />
                              </div>
                           </div>
                           <textarea
                              value={comment}
                              type='text'
                              placeholder='Write Comment'
                              onChange={(e) => setComment(e.target.value)
                              }
                           />
                           <div className='icons'>
                              <div className='choose-emojie'>
                                 {showEmojis && (
                                    <Picker
                                       onSelect={addEmoji}
                                       style={{
                                         position: 'absolute',
                                         bottom: '-385px',
                                         left: '-200px',
                                         zIndex: '1',
                                         borderRadius: '15px',
                                       }}
                                       theme='dark'
                                    />
                                 )}
                                 <div
                                    onClick={() => setShowEmojis(!showEmojis)
                                    }>
                                     <AiOutlineSmile />
                                 </div>
                              </div>
                              <button type='submit'>
                                 <SendSVG />
                              </button>
                           </div>
                        </form>
                     </div>
                  </div>
               </div>
            </div>
         ) : (
           ''
         )}
      </div>
  );
}

export default memo(CommentSection);
