import { Axios, authAxios } from '../../services/auth-header';
import { returnErrors } from './errorActions';

import {
  FOLLOW,
  UNFOLLOW,
  REGISTER,
  LOGOUT,
} from '../types/action-types';

// Check token & load User
export const loadUser = () => async (dispatch) => {
  //   soon
};

// register action
export const register = (formData, navigate) => async (dispatch) => {
  try {
    const { data } = await Axios.post('/auth/register', formData);
    dispatch({ type: REGISTER, payload: data.user });
    // follow admin hard code
    await Axios.put('/auth/follow', {
      userId: data.user._id,
      otherUserId: '618945ce252b453ab69e4cc9',
    });
    // set Local data
    localStorage.setItem('userId', JSON.stringify(data.user._id));
    localStorage.setItem('token', JSON.stringify(data.token));
    navigate('/');
    window.location.reload();
  } catch (error) {
    dispatch(
      returnErrors(error.response.data.msg, error.response.status),
    );
  }
};
// login action
export const login = (formData, navigate) => async (dispatch) => {
  try {
    const { data } = await Axios.post('/auth/login', formData);
    dispatch({ type: 'LOGIN_SUCCESS', payload: data.user });
    // set Local data
    localStorage.setItem('userId', JSON.stringify(data.user._id));
    localStorage.setItem('token', JSON.stringify(data.token));

    navigate('/');
    window.location.reload();
  } catch (error) {
    dispatch(
      returnErrors(error.response.data.msg, error.response.status),
    );
  }
};
// logout action
export const logout = (navigate) => (dispatch) => {
  //   soon
  dispatch({ type: LOGOUT });
  localStorage.clear();
  navigate('/auth');
  window.location.reload();
};

export const followUser = (currentUserId, otherUserId) => async (dispatch) => {
  authAxios.put(`/users/${otherUserId}/follow`, {
    userId: currentUserId,
  });
  dispatch({ type: FOLLOW, payload: otherUserId });
};

export const unfollowUser = (currentUserId, otherUserId) => async (dispatch) => {
  const { data } = authAxios.put(
    `/users/${otherUserId}/unfollow`,
    {
      userId: currentUserId,
    },
  );
  dispatch({ type: UNFOLLOW, payload: otherUserId });
};
