import { useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { ProfileSVG } from '../../Assets/icons/SVG';
import Spinner from '../../components/Spinner/Spinner';

import {
  followUser,
  unfollowUser,
} from '../../Redux/actions/authActions';

function FollowModal({
  isLoading, follow, title, closeModal,
}) {
  const currentUser = useSelector((state) => state.auth.user);
  const dispatch = useDispatch();

  // click outside form modal
  const handleClickOutsideModal = (e) => {
    const target = e.target.classList.contains('follow-modal');
    console.log(target);
    if (target) return closeModal();
  };

  // handle btn Logic ui
  const handleBtnLogic = useCallback((userId) => {
    if (userId && currentUser) {
      if (currentUser._id === userId) return 'You';
      if (currentUser.followings.includes(userId)) {
        return (
               <button
                  className='btn btn-danger'
                  onClick={() => dispatch(unfollowUser(currentUser._id, userId))
                  }>
                  unfollow
               </button>
        );
      }
      if (!currentUser.followings.includes(userId)) {
        return (
               <button
                  className='btn'
                  onClick={() => dispatch(followUser(currentUser._id, userId))
                  }>
                  Follow
               </button>
        );
      }
    }
  }, [currentUser, dispatch]);

  // follow unfollow me the btn logic ui
  useEffect(() => {
    handleBtnLogic();
  }, [follow, handleBtnLogic]);

  if (isLoading) {
    return (
         <div
            className='follow-modal'
            onClick={(e) => handleClickOutsideModal(e)}>
            <div className='follow-modal-container'>
               <div className='title'>
                  <h3>{title}</h3>
               </div>
               <ul className='follow-list'>
                  <div className='spinner '>
                     <Spinner />
                  </div>
               </ul>
            </div>
         </div>
    );
  }

  return (
      <div
         className='follow-modal'
         onClick={(e) => handleClickOutsideModal(e)}>
         <div className='follow-modal-container'>
            <div className='title'>
               <h3>{title}</h3>
            </div>
            <ul className='follow-list'>
               {follow.map(
                 ({
                   _id, username, profilePicture, city, from,
                 }) => (
                     <li key={_id}>
                        <Link
                           onClick={closeModal}
                           to={`/profile/${username}`}
                           className='image-container'>
                           {profilePicture ? (
                              <img
                                 src={profilePicture}
                                 alt='profile picture'
                              />
                           ) : (
                              <ProfileSVG />
                           )}
                        </Link>
                        <Link
                           onClick={closeModal}
                           to={`/profile/${username}`}
                           className='follow-user-info'>
                           <h4>{username}</h4>
                           <p className='caption'>{city || from}</p>
                        </Link>
                        {handleBtnLogic(_id)}
                     </li>
                 ),
               )}
            </ul>
         </div>
      </div>
  );
}

export default FollowModal;
