import { useContext } from 'react';
import { useDispatch } from 'react-redux';
import { deletePost } from '../../../Redux/actions/postActions';

import './more.css';

import { GlobalContext } from '../../../context/GlobalContext';
import {
  DeleteSVG,
  EditSVG,
  BookmarkSVG,
} from '../../../Assets/icons/SVG';

function More({ postId, setIsMoreModalOpen }) {
  const { setCurrentId } = useContext(GlobalContext);
  const { setEdit, setIsModalOpen } = useContext(GlobalContext);

  const dispatch = useDispatch();

  // delete post
  const handleDeletePost = () => {
    dispatch(deletePost(postId));
    setIsMoreModalOpen(false);
  };

  const handleEditPost = (postId) => {
    setEdit(true);
    setCurrentId(postId);
    setIsModalOpen(true);
  };

  return (
      <div className='more-modal'>
         <div className='more-modal-container'>
            <ul>
               <li
                  className='delete'
                  onClick={() => handleDeletePost()}>
                  <DeleteSVG className='delete' />
                  <span>Delete Post</span>
               </li>
               <li onClick={() => handleEditPost(postId)}>
                  <EditSVG />
                  <span>Edit Post</span>
               </li>
               <li>
                  <BookmarkSVG />
                  <span>Save Post</span>
               </li>
            </ul>
         </div>
      </div>
  );
}

export default More;
