import { useState, useEffect, memo } from 'react';
import { format } from 'timeago.js';
import { useDispatch } from 'react-redux';

import { Link } from 'react-router-dom';
import { FiMoreHorizontal, FiShare2 } from 'react-icons/fi';
import {
  BookmarkSVG,
  ChatSVG,
  HeartSVG,
} from '../../../Assets/icons/SVG';

import { authAxios } from '../../../services/auth-header';
import { removeComment } from '../../../Redux/actions/postActions';

function SingleComment({ postId, comment }) {
  const { _id, createdAt, body } = comment;
  const [userInComment, setUserInComment] = useState([]);
  const dispatch = useDispatch();

  // get users in commensts
  useEffect(() => {
    let isSubscribed = true;

    const fetch = async () => {
      const {
        data: { user },
      } = await authAxios.get(
        `/users?userId=${comment.createdBy}`,
      );
      if (isSubscribed) {
        setUserInComment(user);
      }
    };
    fetch();
    return () => (isSubscribed = false);
  }, [comment.createdBy]);

  // get users in commensts when show all comments
  //  useEffect(() => {
  //    if (isCommentsFetch) {
  //       const fetch = async () => {
  //          const {
  //             data: { user },
  //          } = await authAxios.get(
  //             `/users?userId=${comment.createdBy}`
  //          );
  //          setUserInComment(user);
  //       };
  //       fetch();
  //    } else {
  //       return;
  //    }
  // }, [isCommentsFetch]);

  const handleRemoveComment = (commentId, postId) => {
    dispatch(removeComment(commentId, postId));
  };

  const [showSettings, setShowSettings] = useState(false);

  //   useEffect(() => {
  //     console.log('single ceomment rerender');
  //   }, []);

  return (
      <li key={_id} className='single-comment'>
         <div className='user'>
            {true && (
               <div className='image-container'>
                  <img
                     src={userInComment?.profilePicture || ''}
                     alt='user profile image'
                  />
               </div>
            )}
         </div>
         <div className='comment-content'>
            <h4 className='username'>
               <Link to={`/profile/${userInComment.username}`}>
                  {userInComment.displayName}
               </Link>
               <span className='created-at'>
                  {' '}
                  ·{format(createdAt)}
               </span>
               <button
                  aria-label='btn more'
                  className='settings'
                  onClick={() => setShowSettings(!showSettings)}>
                  <FiMoreHorizontal />
                  <ul
                     style={{ top: '-85px', left: '-65px' }}
                     className={`settings-list ${
                       showSettings && 'show'
                     }`}>
                     <li
                        className='remove'
                        onClick={() => handleRemoveComment(_id, postId)
                        }>
                        Remove
                     </li>
                     <li className='fowrwade'>Fowrwade</li>
                  </ul>
               </button>
            </h4>
            <p>{body}</p>
            <div className='action-buttons'>
               <div className='interaction-buttons'>
                  <span className='icons reactions love-icon'>
                     <HeartSVG />
                  </span>
                  <span className='comment-icon'>
                     <ChatSVG />
                  </span>
                  <span className='share-icon'>
                     <FiShare2 />
                  </span>
                  <div className='bookmark'>
                     <span className='bookmark-icon'>
                        <BookmarkSVG />
                     </span>
                  </div>
               </div>
            </div>
         </div>
      </li>
  );
}

export default memo(SingleComment);
