import React, { useState } from 'react';
import { format } from 'timeago.js';
import { useDispatch } from 'react-redux';
import ReactEmoji from 'react-emoji';

import {
  IoHappyOutline,
  IoArrowRedoOutline,
  IoEllipsisVertical,
} from 'react-icons/io5';
import { removeMessage } from '../../../Redux/actions/conversationActions';

function Message({
  socket,
  messages,
  setMessages,
  message,
  own,
  index,
  isRoom,
}) {
  const [showMore, setShowMore] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const dispatch = useDispatch();

  const hanldeRemoveMessage = (messageId) => {
    const filterMessages = messages.filter(
      (msg) => msg._id !== messageId,
    );
    setMessages(filterMessages);
    dispatch(removeMessage(messageId));
  };

  return (
      <li
         onMouseLeave={() => {
           setShowMore(false);
         }}
         onMouseOver={() => {
           setShowMore(true);
         }}
         className={`${own ? 'message own' : 'message'}`}>
         <div className='message-content'>
            <p
               className='message-caption'
               data-username={message.user}
               data-timestamp={format(
                 message.createdAt,
                 'round-minute',
               )}>
               {ReactEmoji.emojify(message.text)}
            </p>
         </div>
         {!isRoom && <div className={`more ${showMore && 'show'}`}>
            <button className='emojie'>
               <IoHappyOutline />
            </button>
            <button className='reply'>
               <IoArrowRedoOutline />
            </button>
            <button
               aria-label='btn more'
               className='settings'
               onClick={() => setShowSettings(!showSettings)}>
               <IoEllipsisVertical />
               <ul
                  className={`settings-list ${
                    showSettings && 'show'
                  }`}>
                  <li
                     className='remove'
                     onClick={() => hanldeRemoveMessage(message._id)}>
                     Remove
                  </li>
                  <li className='fowrwade'>Fowrwade</li>
               </ul>
            </button>
         </div>}

      </li>
  );
}

export default Message;
