import React, {
  useState, useEffect, useRef, useContext,
} from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { format } from 'timeago.js';

// icons
import {
  IoEllipsisHorizontal,
  IoCalendarOutline,
  IoClose,
  IoLocationOutline,
  IoUnlink,
} from 'react-icons/io5';
import { authAxios } from '../../services/auth-header';

import { MessageSVG } from '../../Assets/icons/SVG';

// redux action functions
import { followUser, unfollowUser } from '../../Redux/actions/authActions';
import { createConversation } from '../../Redux/actions/conversationActions';

import {
  getUserFriends,
  getUserFollowers,
  getUserFollowing,
} from '../../Redux/actions/userActions';

// modals
import FollowModal from './FollowModal';
import EditUserModal from './EditUserModal';
import { GlobalContext } from '../../context/GlobalContext';

function User() {
  const dispatch = useDispatch();

  const { socket } = useContext(GlobalContext);

  const username = useParams().id;

  const [user, setUser] = useState([]);
  const currentUser = useSelector((state) => state.auth.user);

  const [isEditUserModalOpen, setIsEditUserModalOpen] = useState(false);

  // is Follow Modal Open
  const [isFollowersModalOpen, setIsFollowersModalOpen] = useState(false);
  const [isFollowingModalOpen, setIsFollowingModalOpen] = useState(false);

  const [closeProfileImage, setCloseProfileImage] = useState(false);
  const [closeCoverImage, setCloseCoverImage] = useState(false);

  // --------------FOLLOW ------------------
  const [isFollowing, setIsFollowing] = useState(
    currentUser?.followings?.includes(user?._id),
  );

  const imageProfilePic = useRef(null);

  // set liked dislike
  useEffect(() => {
    setIsFollowing(currentUser?.followings?.includes(user?._id));
  }, [currentUser, user._id]);

  // GET User Profile
  useEffect(() => {
    let isFetching = true;
    const fetchUser = async () => {
      const { data } = await authAxios.get(`/users?username=${username}`);
      if (isFetching) {
        setUser(data.user);
      }
    };
    username && fetchUser();
    return () => {
      isFetching = false;
    };
  }, [username, currentUser]);

  // Get userFriends
  useEffect(() => {
    dispatch(getUserFriends(user._id));
  }, [user._id, dispatch]);

  // followers && Following
  const { followers, following, isLoading } = useSelector(
    (state) => state.user,
  );

  // close Modal
  const closeModal = () => {
    setIsFollowersModalOpen(false);
    setIsFollowingModalOpen(false);
  };

  // followers and following
  const handleGetFollowing = () => {
    setIsFollowersModalOpen(false);
    setIsFollowingModalOpen(true);
    dispatch(getUserFollowing(user._id));
  };

  const handleGetFollowers = () => {
    setIsFollowersModalOpen(true);
    setIsFollowingModalOpen(false);
    dispatch(getUserFollowers(user._id));
  };

  const sendNotification = (actionType, objectName, ref, id) => {
    socket.emit('sendNotification', {
      senderId: currentUser._id,
      senderName: currentUser.displayName,
      senderImg: currentUser.profilePicture || '',
      receiverId: id,
      time: Date.now(),
      actionType,
      objectName,
      ref,
    });
  };

  // HANDLE CLICK TO FOLLOW User
  const handleClickToFollow = async () => {
    if (isFollowing) {
      // unfollow
      dispatch(unfollowUser(currentUser._id, user._id));
      sendNotification('unfollow', 'profile', currentUser.username, user._id);
    } else {
      // follow
      dispatch(followUser(currentUser._id, user._id));
      sendNotification('follow', 'profile', currentUser.username, user._id);
    }
    setIsFollowing(!isFollowing);
  };

  // click outside form modal
  const handleClickOutsideModal = (e) => {
    const target = e.target.classList.contains('cover-picture')
      || e.target.classList.contains('profile-picture');
    if (target) {
      setCloseProfileImage(false);
      setCloseCoverImage(false);
    }
  };

  // handle edit USer
  const handleEditUser = () => {
    setIsEditUserModalOpen(true);
  };

  return (
    <section className="user-cover">
      <div
        onClick={(e) => handleClickOutsideModal(e)}
        className={`cover-picture ${closeCoverImage && 'preview '}${
          user.coverPicture && 'true'
        }`}
        data-username={user.username}
      >
        {user.coverPicture && (
          <img
            onClick={() => setCloseCoverImage(true)}
            src={user.coverPicture}
            alt="user cover photo"
          />
        )}
        <span className="close" onClick={() => setCloseCoverImage(false)}>
          <IoClose />
        </span>
      </div>

      <div className="btns">
        {currentUser._id !== user._id && (
          <button
            className={`btn follow-btn ${
              !isFollowing ? 'follow' : 'unfollow'
            } `}
            onClick={() => handleClickToFollow()}
          >
            {isFollowing ? 'unfollow' : 'follow'}
          </button>
        )}
        {currentUser._id === user._id && (
          <button className="edit-profile" onClick={handleEditUser}>
            edit Profile
          </button>
        )}
        <button className="btn more">
          <IoEllipsisHorizontal />
        </button>

        {user._id !== currentUser._id && (
          <button
            className="btn send-message"
            onClick={() => {
              dispatch(createConversation(currentUser._id, user._id));
            }}
          >
            <MessageSVG />
          </button>
        )}
      </div>
      <div
        onClick={(e) => handleClickOutsideModal(e)}
        ref={imageProfilePic}
        className={`profile-picture ${closeProfileImage && 'preview'}`}
        data-username={user?.username?.charAt(0)}
      >
        {user.profilePicture && (
          <img
            onClick={() => setCloseProfileImage(true)}
            src={user.profilePicture}
            alt="user profile"
          />
        )}
        <span className="close" onClick={() => setCloseProfileImage(false)}>
          <IoClose />
        </span>
      </div>
      <div className="user-info">
        <h2 className="username">{user?.displayName}</h2>
        <p className="about">{user.about}</p>
        <section className="more-info">
          {user.adress && (
            <div className="city">
              <IoLocationOutline />
              <span>{user.adress.city || user.adress.from}</span>
            </div>
          )}
          {user?.links?.website && (
            <a href={user.links.website} target="_blank" className="link" rel="noreferrer">
              <IoUnlink />
              <span>
                {user.links.website.split('//')[1] || user.links.website}
              </span>
            </a>
          )}
          <div className="account-date">
            <IoCalendarOutline />
            <span>{format(user.createdAt, 'twitter')}</span>
          </div>
        </section>
        <div className="follow-section">
          <a>
            <span className="follow-count" onClick={handleGetFollowing}>
              <b>{user?.followings?.length}</b>
              {' '}
              Following
            </span>
          </a>
          <a>
            <span className="follow-count" onClick={handleGetFollowers}>
              <b>{user?.followers?.length}</b>
              {' '}
              Followers
            </span>
          </a>
          {isFollowingModalOpen && (
            <FollowModal
              follow={following}
              title="following"
              closeModal={closeModal}
              isLoading={isLoading}
            />
          )}
          {isFollowersModalOpen && (
            <FollowModal
              follow={followers}
              title="followers"
              closeModal={closeModal}
              isLoading={isLoading}
            />
          )}
        </div>

        {/* <div className='tags'>
               Tags :<span>HipHop</span>
               <span>Music</span>
               <span>Playing Games</span>
            </div> */}
      </div>
      <EditUserModal
        isEditUserModalOpen={isEditUserModalOpen}
        setIsEditUserModalOpen={setIsEditUserModalOpen}
      />
    </section>
  );
}

export default React.memo(User);
