import React, { useState, useEffect, memo } from 'react';
import { FiMoreHorizontal } from 'react-icons/fi';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { format } from 'timeago.js';
import { ProfileSVG } from '../../../Assets/icons/SVG';
import More from '../PostMore/index';
import UserCard from './UserCard';

function User({ user, post }) {
  const [popupOpen, setPopupOpen] = useState(false);
  const [isMoreModalOpen, setIsMoreModalOpen] = useState(false);
  // post more setting
  const toggleMoreModal = () => {
    setIsMoreModalOpen(!isMoreModalOpen);
  };

  const userId = useSelector((state) => state.auth.user._id);
  const {
    _id,
    caption,
    images,
    videosUrl,
    createdAt,
    createdBy,
    likes,
    comments,
    setCurrentId,
  } = post;

  //   console.log('user owner post call');

  return (
      <div className='head'>
         <div
            className='user'
            onMouseOver={() => setPopupOpen(true)}
            onMouseLeave={() => setPopupOpen(false)}>
            <Link
               className='profile-photo'
               to={`/profile/${user.username}`}>
               {user?.profilePicture ? (
                  <img
                     src={user.profilePicture}
                     alt={`${user.displayName} Profile`}
                  />
               ) : (
                  <ProfileSVG />
               )}
            </Link>
            <div className='ingo'>
               <Link to={`/profile/${user.username}`}>
                  <h3>{user.displayName}</h3>
               </Link>
               <small>
                  Dubai, {' '}
                  {format(createdAt)}
               </small>
            </div>
            <UserCard
               {...user}
               currentUserId={userId}
               popupOpen={popupOpen}
            />
         </div>
         {/* more post setting */}
         {userId === createdBy && (
            <span className='edit more-icon'>
               <FiMoreHorizontal onClick={toggleMoreModal} />
               {isMoreModalOpen && (
                  <More
                     postId={_id}
                     setIsMoreModalOpen={setIsMoreModalOpen}
                  />
               )}
            </span>
         )}
      </div>
  );
}

export default memo(User);
