import React, {
  useState,
  useEffect,
  useRef,
  useContext,
  useMemo,
  useCallback,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { authAxios } from '../../services/auth-header';
import { GlobalContext } from '../../context/GlobalContext';
import { likePost } from '../../Redux/actions/postActions';

import User from './PostOwner/User';
import ImagesGrid from './FilesGrid/Images';
import Caption from './Caption';
import ActionButtons from './ActionButtons/index';
import LikedBy from './LikedBy/index';
import CommentSection from './Comments/CommentsSection';

import './post.css';

function Post({ post }) {
  const {
    _id,
    caption,
    images,
    videosUrl,
    createdBy,
    likes,
    comments,
  } = post;
  const { socket } = useContext(GlobalContext);
  const { isPostLoading } = useSelector((state) => state.post);

  // Current User
  const [user, setUser] = useState([]);
  const [isLiked, setIsLiked] = useState(false);
  // const [isMoreModalOpen, setIsMoreModalOpen] = useState(false);

  // get random user for display name in post
  const [otherUserId, setOtherUserId] = useState(null);
  const [randomUser, setRandomUser] = useState(null);

  // marge images and videos
  let files = useMemo(() => [], []);
  if (images && images[0]) files = [...files, ...images];
  if (videosUrl && videosUrl[0]) files = [...files, ...videosUrl];

  const dispatch = useDispatch();
  const {
    _id: userId,
    profilePicture,
    displayName,
  } = useSelector((state) => state.auth.user);
  const { posts } = useSelector((state) => state.post);

  // UI logic for who "liked post" &&  (set not me if i'm the last liked post)

  const handleUserLikePost = useCallback(
    () => {
      const lastUserIdInPost = likes[likes.length - 1];

      if (lastUserIdInPost === userId) {
        setOtherUserId(likes[likes.length - 2]);
      } else {
        setOtherUserId(likes[likes.length - 1]);
      }
    },
    [likes, userId],
  );

  // get posts
  useEffect(() => {
    // setOtherUserId(likes[likes.length - 1]);
    let isFetching = true;
    const fetchUser = async () => {
      if (otherUserId) {
        const { data } = await authAxios.get(
          `/users?userId=${otherUserId}`,
        );
        if (isFetching) {
          setRandomUser(data.user);
        }
      }
    };
    fetchUser();
    handleUserLikePost(otherUserId);
    return () => {
      isFetching = false;
    };
  }, [otherUserId, handleUserLikePost]);

  // get the Creator this post
  useEffect(() => {
    let isSubscribed = true;
    const fetchUser = async () => {
      if (createdBy) {
        const { data } = await authAxios.get(
          `/users?userId=${createdBy}`,
        );
        if (isSubscribed) {
          setUser(data.user);
        }
      }
    };
    fetchUser();
    return () => isSubscribed = false;
  }, [createdBy]);

  // is currnet user Like this Post >
  useEffect(() => {
    setIsLiked(likes.includes(userId));
  }, [userId, likes]);

  // post more setting
  // const toggleMoreModal = () => {
  //    setIsMoreModalOpen(!isMoreModalOpen);
  // };

  // big text if paragrafe small and no post image
  const [bigText, setBigText] = useState(false);
  const captionRef = useRef(null);
  useEffect(() => {
    if (caption && !files[0]) {
      setBigText(true);
      if (caption.length < 70) {
        if (captionRef) {
          captionRef.current.style.fontSize = '23px';
        }
      }
      if (caption.length > 70 && caption.length < 100) {
        if (captionRef) {
          captionRef.current.style.fontSize = '17px';
        }
      }
    }
  }, [posts, caption, files]);

  const sendNotification = (actionType, objectName, ref) => {
    socket.emit('sendNotification', {
      senderId: userId,
      senderName: displayName,
      senderImg: profilePicture || '',
      receiverId: createdBy,
      time: Date.now(),
      actionType,
      objectName,
      ref,
    });
  };

  // like Post
  const handleLikeBtn = (actionType, objectName, ref) => {
    dispatch(likePost(_id, userId));
    setIsLiked(!isLiked);
    sendNotification(actionType, objectName, ref);
  };

  // console.log('post call ----------------------------------------------------------------');

  return (
      <div className={`feed ${bigText ? 'big-text' : ''}`}>

         <User user={user} post={post} />
         {/* files Preview */}
         {files && files[0] && (
            <ImagesGrid
               user={user}
               post={post}
               files={files}
               images={images}
               videosUrl={videosUrl}
               handleLikeBtn={handleLikeBtn}
               isLiked={isLiked}
            />
         )}

         <ActionButtons
            postId={_id}
            handleLikeBtn={handleLikeBtn}
            isLiked={isLiked}
            sendNotification={sendNotification}
         />
         <LikedBy
            likes={likes}
            setIsLiked={setIsLiked}
            userId={userId}
            randomUser={randomUser}
         />
         <Caption
            user={user}
            caption={caption}
            captionRef={captionRef}
         />
         <CommentSection postId={_id} comments={comments} />
      </div>
  );
}

export default React.memo(Post);
