import {
  WATCH_STORIE,
  REACT_TO_STORIE,
  SET_LOADING,
} from '../types/action-types';

const initialState = {
  stories: [],
  views: [],
  isLoading: false,
};

const storieReducer = (state = initialState, action) => {
  switch (action.type) {
    case WATCH_STORIE:
      return { ...state };
    case REACT_TO_STORIE:
      return { ...state };
    case SET_LOADING:
      return { ...state, isLoading: true };
    default:
      return state;
  }
};

export default storieReducer;
